import classnames from 'classnames';
import { useEffect } from 'react';

import { useComponentVisible } from '..';
import { SelectInput } from '../Select/SelectInput';
import { useGetMultiSelectInputText } from './hooks/useGetMultiSelectInputText';
import { useGetSelectedOptionsFromKeys } from './hooks/useGetSelectedOptions';
import { MultiSelectDropdown } from './MultiSelectDropdown';
import { MultiSelectTooltipContainer } from './MultiSelectTooltipContainer';

export interface MultiSelectOption {
  key: string;
  value: string;
}

export interface MultiSelectLiterals {
  selectLabel: string;
  selectPlaceholder: string;
  applyLabel: string;
  cancelLabel: string;
  emptyOptionsText: string;
  selectAllLabel: string;
  searchInputLabel: string;
}

export interface MultiSelectProps {
  name: string;
  className?: string;
  literals: MultiSelectLiterals;
  options: MultiSelectOption[];
  expanded?: boolean;
  withTooltip?: boolean;
  selectedKeys: string[];
  onSetSelectedKeys: (newOptions: string[]) => void;
  onCustomSearch?: (searchTerms: string) => void;
}

export const MultiSelect = ({
  name,
  className,
  literals,
  options,
  expanded = false,
  withTooltip = false,
  selectedKeys,
  onSetSelectedKeys,
  onCustomSearch,
}: MultiSelectProps) => {
  const { selectLabel, selectPlaceholder } = literals;

  const {
    visibleRef,
    isComponentVisible: isSelectActive,
    setIsComponentVisible: setIsSelectActive,
  } = useComponentVisible(expanded);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => setIsSelectActive(false), [selectedKeys]);

  const selectedOptions = useGetSelectedOptionsFromKeys(options, selectedKeys);

  const handleInputClick = () => setIsSelectActive((isActive) => !isActive);

  const handleCancelClick = () => setIsSelectActive(false);

  const handleApplyClick = (newSelection: string[]) =>
    onSetSelectedKeys(newSelection);

  const inputContent = useGetMultiSelectInputText(
    selectedOptions,
    selectPlaceholder,
  );

  return (
    <div className={classnames('multiSelect', className)} ref={visibleRef}>
      <MultiSelectTooltipContainer
        selectedOptions={selectedOptions}
        showTooltip={withTooltip}
      >
        <SelectInput
          className="is-active"
          content={inputContent}
          data-testid="multiSelectButtonInput"
          id={name}
          isDropdownVisible={isSelectActive}
          label={selectLabel}
          name={name}
          onClick={() => handleInputClick()}
          type="text"
          value={inputContent}
        />
      </MultiSelectTooltipContainer>

      {(isSelectActive || expanded) && (
        <MultiSelectDropdown
          isSelectActive={isSelectActive}
          literals={literals}
          onApply={handleApplyClick}
          onCancel={handleCancelClick}
          onCustomSearch={onCustomSearch}
          options={options}
          selectedKeys={selectedKeys}
          visibleRef={visibleRef}
        />
      )}
    </div>
  );
};
