import { IcCircleCross, IcSearch } from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { forwardRef, useEffect, useRef, useState } from 'react';
import mergeRefs from 'react-merge-refs';

import { Input } from '../Input';

export interface SearchProps
  extends Omit<React.ComponentProps<typeof Input>, 'onChange' | 'children'> {
  onChange?: (val: string) => void;
  minChars?: number;
  minCharsHelper?: string;
}

export const Search = forwardRef(
  (
    { minChars = 0, minCharsHelper, ...otherProps }: SearchProps,
    ref: React.RefObject<HTMLInputElement>,
  ) => {
    const [value, setValue] = useState(otherProps.value);
    const [helperVisible, setHelperVisible] = useState(false);
    const [resetVisible, setResetVisible] = useState(false);
    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    useEffect(() => {
      if (otherProps.onChange) {
        otherProps.onChange(value ?? '');
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [value]);
    useEffect(() => {
      if (
        typeof otherProps.value !== 'undefined' &&
        otherProps.value !== value
      ) {
        setValue(otherProps.value);
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [otherProps.value]);

    useEffect(() => {
      setHelperVisible(value!.length < minChars && value!.length > 0);
    }, [value, minChars]);
    useEffect(() => {
      setResetVisible(value!.length > 0);
    }, [value, minChars]);

    const toggleResetIcon = () => {
      setValue('');
      inputRef?.current?.focus();
    };

    const SearchVisibility = () => (
      <>
        {helperVisible && (
          <span className="formField_icon formField_inlineHelper">
            {minCharsHelper}
          </span>
        )}
        {resetVisible && (
          <IcCircleCross
            className="formField_icon formField_icon-clean"
            data-testid="clean"
            onClick={toggleResetIcon}
            onMouseDown={(e: React.MouseEvent) => e.preventDefault()}
          />
        )}
      </>
    );

    return (
      <Input
        dataMarshalComponent="Search"
        {...otherProps}
        className={classnames(otherProps.className, 'formField-search')}
        LeadingIcon={IcSearch}
        onChange={(e) => setValue(e.target.value)}
        PostComponent={<SearchVisibility />}
        ref={combinedRefs}
        type="search"
        value={value}
      />
    );
  },
);
