color.$state-light-primary-active,\n    ),\n    disabled: (\n      color: color.$text-light-disabled,\n    ),\n  ),\n  destructive: (\n    default: (\n      background-color: color.$surface-light-negative,\n      color: color.$state-light-negative-active,\n    ),\n    hover: (\n      color: color.$state-light-negative-hover,\n      box-shadow: inset 0 0 0 2px color.$border-light-negative,\n    ),\n    active: (\n      color: color.$state-light-negative-active,\n      box-shadow: inset 0 0 0 2px color.$state-light-negative-active,\n    ),\n    disabled: (\n      color: color.$text-light-disabled,\n      background-color: color.$surface-light-secondary,\n    ),\n  ),\n);\n\n$button-spinners: (\n  primary: \"inverted.png\",\n  secondary: \"primary.png\",\n  tertiary: \"primary.png\",\n  destructive: \"negative.png\",\n);\n\n@each $button-key, $button-value in $buttons {\n  $button-spinner: map.get($button-spinners, $button-key);\n\n  .button-#{$button-key} {\n    @each $default-key, $default-value in map.get($button-value, \"default\") {\n      #{$default-key}: $default-value;\n    }\n    &:hover,\n    &:focus {\n      @each $default-key, $default-value in map.get($button-value, \"hover\") {\n        #{$default-key}: $default-value;\n      }\n    }\n    &:active {\n      @each $default-key, $default-value in map.get($button-value, \"active\") {\n        #{$default-key}: $default-value;\n      }\n    }\n    &.is-disabled {\n      pointer-events: none;\n      @each $default-key, $default-value in map.get($button-value, \"disabled\") {\n        #{$default-key}: $default-value;\n      }\n      .buttonMotion {\n        opacity: 0.7;\n      }\n    }\n    .buttonMotion_spinner {\n      background: url(layout.$progressIndicatorsUrl + $button-spinner)\n        center\n        center /\n        cover\n        no-repeat;\n    }\n  }\n}\n\n.buttonMotion {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n\n  &_spinner {\n    width: layout.$spacing * 2;\n   