ight-negative;\n    color: color.$text-dark-primary;\n  }\n}\n.button-secondary,\n.button-destructive {\n  .buttonMotion {\n    &-saved {\n      background-color: color.$surface-light-positive;\n    }\n    &-failed {\n      background-color: color.$surface-light-negative;\n    }\n  }\n}\n","@use \"../../../styles/layout\";\n@use \"../../../styles/tokens/color\";\n@use \"../../../styles/typography\";\n\n.actionButton {\n  @include typography.typography(\"body\");\n  display: inline-flex;\n  align-items: center;\n  padding: 0;\n  border: 0;\n  background-color: transparent;\n  text-align: left;\n  text-decoration: none;\n  cursor: pointer;\n\n  &.is-loading {\n    cursor: not-allowed;\n    pointer-events: none;\n  }\n\n  &_loadingIcon {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    animation: rotate 0.8s infinite linear;\n  }\n\n  &.is-disabled,\n  [disabled] {\n    color: color.$text-light-disabled;\n    cursor: not-allowed;\n    pointer-events: none;\n\n    .actionButton_icon {\n      background: color.$surface-light-secondary;\n      color: color.$text-light-disabled;\n    }\n  }\n\n  // SIZES\n  // TODO: set border radius sizes in layout.scss\n  &-xs .actionButton_icon {\n    margin-right: layout.$spacing;\n    width: layout.$spacing * 3;\n    height: layout.$spacing * 3;\n    border-radius: 6px;\n  }\n\n  &-sm .actionButton_icon {\n    margin-right: layout.$spacing * 2;\n    width: layout.$spacing * 4;\n    height: layout.$spacing * 4;\n    border-radius: 8px;\n  }\n\n  &-md .actionButton_icon {\n    margin-right: layout.$spacing * 2;\n    width: layout.$spacing * 5;\n    height: layout.$spacing * 5;\n    border-radius: 10px;\n  }\n\n  &-lg {\n    display: initial;\n    text-align: center;\n    .actionButton_icon {\n      width: layout.$spacing * 7;\n      height: layout.$spacing * 7;\n      border-radius: 14px;\n    }\n    .actionButton_content {\n      display: block;\n      margin-top: layout.$spacing;\n    }\n  }\n\n  // VARIANTS\n  &-primary {\n    color: color.$action-light-primary;\n\n    .actionButton_loadingIcon {\n      background: url(layout.$progressIndicatorsUrl + \"primary.png\") center\n        center / (layout.$spacing * 2) no-repeat;\n    }\n\n    .actionButton_icon {\n      background: color.$surface-light-primary;\n    }\n\n    &:hover {\n      color: color.$state-light-primary-hover;\n\n      .actionButton_icon {\n        border: solid 2px color.$border-light-primary;\n      }\n    }\n\n    &:active {\n      color: color.$state-light-primary-active;\n\n      .actionButton_icon {\n        border: solid 2px color.$state-light-primary-active;\n      }\n    }\n  }\n\n  &-secondary {\n    color: color.$text-light-secondary;\n\n    .actionButton_loadingIcon {\n      background: url(layout.$progressIndicatorsUrl + \"secondary.png\") center\n        center / (layout.$spacing * 2) no-repeat;\n    }\n\n    .actionButton_icon {\n      border: solid 1px color.$border-light-secondary;\n      background: color.$background-light-base;\n      color: color.$info-light-static;\n    }\n  }\n\n  &-destructive {\n    color: color.$state-light-negative-active;\n\n    .actionButton_loadingIcon {\n      background: url(layout.$progressIndicatorsUrl + \"negative.png\") center\n        center / (layout.$spacing * 2) no-repeat;\n    }\n\n    .actionButton_icon {\n      background: color.$surface-light-negative;\n    }\n\n    &:hover {\n      color: color.$state-light-negative-hover;\n\n      .actionButton_icon {\n        border: solid 2px color.$border-light-negative;\n      }\n    }\n\n    &:active {\n      color: color.$state-light-negative-active;\n\n      .actionButton_icon {\n        border: solid 2px color.$state-light-negative-active;\n      }\n    }\n  }\n}\n\n.actionButton_icon {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n  flex-shrink: 0;\n  justify-content: center;\n  padding: 0;\n  vertical-align: middle;\n  text-align: center;\n  cursor: pointer;\n}\n\n.actionButton-n