ht-secondary;\n}\n","@use \"../../styles/tokens/color\";\n@use \"../../styles/layout\";\n@use \"../../styles/motion\";\n@use \"../../styles/shadow\";\n@use \"../../styles/responsive\";\n\n.whisper {\n  width: layout.$spacing * 40;\n  box-shadow: shadow.$rised;\n\n  .notification_actionIcon svg {\n    color: color.$action-dark-secondary;\n  }\n\n  &-success {\n    .whisper_typeDescriptor {\n      color: color.$info-dark-positive;\n    }\n  }\n\n  &-active {\n    .whisper_typeDescriptor {\n      animation: rotate 0.8s infinite linear;\n    }\n  }\n\n  &_icon {\n    margin: 2px 20px 0 4px;\n  }\n\n  &_avatar {\n    position: relative;\n    margin: -6px 12px -6px -4px;\n    &IconWrapper {\n      position: absolute;\n      right: -2px;\n      bottom: -2px;\n      padding: 3px;\n      width: 14px;\n      height: 14px;\n      border-radius: 50%;\n      background-color: color.$background-dark-base;\n    }\n    &Icon {\n      display: block;\n      width: 100%;\n      height: 100%;\n      color: color.$background-light-base;\n    }\n  }\n}\n\n@include responsive.mq($until: desktop) {\n  .whisper {\n    padding: layout.$spacing * 2;\n\n    &_avatar {\n      margin-top: -4px;\n      margin-bottom: -4px;\n    }\n  }\n}\n","@use \"@product/marshal/dist/styles/layout\";\n@use \"@product/marshal/dist/styles/tokens/color\";\n@use \"@product/marshal/dist/styles/tools/mq\";\n\n$stepIndicator_icon-animation-duration: 1s;\n\n.stepIndicator_icon {\n  margin-right: layout.$spacing;\n  margin-bottom: 4px;\n\n  @include mq.mq($until: desktop) {\n    margin-right: unset;\n  }\n\n  &-done,\n  &-previous {\n    color: color.$info-light-positive;\n  }\n\n  &-previous {\n    animation: stepIndicator_iconToPreviousState\n      $stepIndicator_icon-animation-duration ease;\n    animation-fill-mode: forwards;\n  }\n\n  &-current {\n    color: color.$text-light-primary;\n    animation: stepIndicator_iconToCurrentState\n      $stepIndicator_icon-animation-duration ease;\n    animation-fill-mode: forwards;\n  }\n\n  &-next {\n    color: color.$info-light-static;\n  }\n}\n\n@keyframes stepIndicator_iconToPreviousState {\n  0% {\n    transform: scale(0%);\n  }\n  50% {\n    transform: scale(100%);\n  }\n  100% {\n    transform: scale(100%);\n  }\n}\n\n@keyframes stepIndicator_iconToCurrentState {\n  0% {\n    opacity: 0;\n    transform: translateX(-16px);\n  }\n  33% {\n    opacity: 0;\n    transform: translateX(-16px);\n  }\n  100% {\n    opacity: 100%;\n    transform: translateX(0);\n  }\n}\n","$assetsUrl: \"https://assets.cabifil.es/\" !default;\n$fontsUrl: $assetsUrl + \"fonts/\" !default;\n$iconsUrl: $assetsUrl + \"images/icons/\" !default;\n$progressIndicatorsUrl: $assetsUrl + \"images/progress_indicators/\" !default;\n\n$spacing: 8px;\n\n$radius: 4px;\n","///  This is a substitution of sass-mq functionality made to [manage media queries](https://github.com/sass-mq/sass-mq).\n///  Since this library now is not maintain regularly, we port the functionality to our own repo so that the user can use the same syntax as the sass-mq functionallity.\n\n@use \"sass:math\";\n@use \"sass:map\";\n@use \"sass:list\";\n\n/// Breakpoint list\n///\n/// Name your breakpoints in a way that creates a ubiquitous language\n/// across team members. It will improve communication between\n/// stakeholders, designers, developers, and testers.\n///\n/// @type Map\n/// @link https://github.com/sass-mq/sass-mq#seeing-the-currently-active-breakpoint Full documentation and examples\n$breakpoints: (\n  mobile: 320px,\n  tablet: 740px,\n  desktop: 980px,\n  wide: 1300px,\n) !default;\n\n/// Show breakpoints in the top right corner\n///\n/// If you want to display the currently active breakpoint in the top\n/// right corner of your site during development, add the breakpoints\n/// to this list, ordered by width. For example: (mobile, tablet, desktop).\n///\n/// @example scss\n///   $show-breakpoints: (mobile, tablet, desktop);\n///   @import 'path/to/mq';\n///\n/// @type map\n$show-breakpoints: () !default;\n\n/// Customize the media type (for example: `@media screen` or `@media print`)\n/// By default sass-mq uses an \"all\" media type (`@media all and …`)\n///\n/// @type String\n/// @link https://github.com/sass-mq/sass-mq#changing-media-type Full documentation and example\n$media-type: all !default;\n\n/// Convert pixels to ems\n///\n/// @param {Number} $px - value to convert\n///\n/// @example scss\n///  $font-size-in-ems: px2em(16px);\n///  p { font-size: px2em(16px); }\n///\n/// @returns {Number}\n\n@function px2em($px) {\n  @if math.is-unitless($px) {\n    @warn \"Assuming #{$px} to be in pixels, attempting to convert it into pixels.\";\n    @return px2em($px * 1px);\n  }\n  // if $px is compatible with em units then return value unchanged\n  @if math.compatible($px, 1em) {\n    @return $px;\n  }\n  @return math.div($px, 16px) * 1em;\n}\n\n/// Get a breakpoint's width\n///\n/// @param {String} $name - Name of the breakpoint. One of $breakpoints\n///\n/// @example scss\n///  $tablet-width: get-breakpoint-width(tablet);\n///  @media (min-width: get-breakpoint-width(desktop)) {}\n///\n/// @requires {Variable} $breakpoints\n///\n/// @returns {Number} Value in pixels\n@function get-breakpoint-width($name, $breakpoints: $breakpoints) {\n  @if map.has-key($breakpoints, $name) {\n    @return map.get($breakpoints, $name);\n  } @else {\n    @warn \"Breakpoint #{$name} wasn't found in $breakpoints.\";\n    @return null;\n  }\n}\n\n/// Media Query mixin\n///\n/// @param {String | Boolean} $from [false] - One of $breakpoints\n/// @param {String | Boolean} $until [false] - One of $breakpoints\n/// @param {String | Boolean} $and [false] - Additional media query parameters\n/// @param {String} $media-type [$media-type] - Media type: screen, print…\n///\n/// @ignore Undocumented API, for advanced use only:\n/// @ignore @param {Map} $breakpoints [$breakpoints]\n///\n/// @content styling rules, wrapped into a @media query when $responsive is true\n///\n/// @requires {Variable} $media-type\n/// @requires {Variable} $breakpoints\n/// @requires {function} px2em\n/// @requires {function} get-breakpoint-width\n///\n/// @link https://github.com/sass-mq/sass-mq#responsive-mode-on-default Full documentation and examples\n///\n/// @example scss\n///  .element {\n///    @include mq($from: mobile) {\n///      color: red;\n///    }\n///    @include mq($until: tablet) {\n///      color: blue;\n///    }\n///    @include mq(mobile, tablet) {\n///      color: green;\n///    }\n///    @include mq($from: tablet, $and: '(orientation: landscape)') {\n///      color: teal;\n///    }\n///    @include mq(950px) {\n///      color: hotpink;\n///    }\n///    @include mq(tablet, $media-type: screen) {\n///      color: hotpink;\n///    }\n///    // Advanced use:\n///    $my-breakpoints: (L: 900px, XL: 1200px);\n///    @include mq(L, $breakpoints: $my-breakpoints) {\n///      color: hotpink;\n///    }\n///  }\n@mixin mq(\n  $from: false,\n  $until: false,\n  $and: false,\n  $media-type: $media-type,\n  $breakpoints: $breakpoints\n) {\n  $min-width: 0;\n  $max-width: 0;\n  $media-query: \"\";\n\n  // From: this breakpoint (inclusive)\n  @if $from {\n    @if type-of($from) == number {\n      $min-width: px2em($from);\n    } @else {\n      $min-width: px2em(get-breakpoint-width($from, $breakpoints));\n    }\n  }\n\n  // Until: that breakpoint (exclusive)\n  @if $until {\n    @if type-of($until) == number {\n      $max-width: px2em($until);\n    } @else {\n      $max-width: px2em(get-breakpoint-width($until, $breakpoints)) - 0.01em;\n    }\n  }\n\n  @if $min-width != 0 {\n    $media-query: \"#{$media-query} and (min-width: #{$min-width})\";\n  }\n  @if $max-width != 0 {\n    $media-query: \"#{$media-query} and (max-width: #{$max-width})\";\n  }\n  @if $and {\n    $media-query: \"#{$media-query} and #{$and}\";\n  }\n\n  // Remove unnecessary media query prefix 'all and '\n  @if ($media-type == \"all\" and $media-query != \"\") {\n    $media-type: \"\";\n    $media-query: str-slice(unquote($media-query), 6);\n  }\n\n  @media #{$media-type + $media-query} {\n    @content;\n  }\n}\n\n/// Quick sort\n///\n/// @author Sam Richards\n/// @access private\n/// @param {List} $list - List to sort\n/// @returns {List} Sorted List\n@function _quick-sort($list) {\n  $less: ();\n  $equal: ();\n  $large: ();\n\n  @if length($list) > 1 {\n    $seed: list.nth($list, math.ceil(math.div(length($list), 2)));\n\n    @each $item in $list {\n      @if ($item == $seed) {\n        $equal: list.append($equal, $item);\n      } @else if ($item < $seed) {\n        $less: list.append($less, $item);\n      } @else if ($item > $seed) {\n        $large: list.append($large, $item);\n      }\n    }\n\n    @return join(join(_quick-sort($less), $equal), _quick-sort($large));\n  }\n\n  @return $list;\n}\n\n/// Sort a map by values (works with numbers only)\n///\n/// @access private\n/// @param {Map} $map - Map to sort\n/// @returns {Map} Map sorted by value\n@function _map-sort-by-value($map) {\n  $map-sorted: ();\n  $map-keys: map.keys($map);\n  $map-values: map.values($map);\n  $map-values-sorted: _quick-sort($map-values);\n\n  // Reorder key/value pairs based on key values\n  @each $value in $map-values-sorted {\n    $index: index($map-values, $value);\n    $key: list.nth($map-keys, $index);\n    $map-sorted: map.merge(\n      $map-sorted,\n      (\n        $key: $value,\n      )\n    );\n\n    // Unset the value in $map-values to prevent the loop\n    // from finding the same index twice\n    $map-values: list.set-nth($map-values, $index, 0);\n  }\n\n  @return $map-sorted;\n}\n\n/// Add a breakpoint\n///\n/// @param {String} $name - Name of the breakpoint\n/// @param {Number} $width - Width of the breakpoint\n///\n/// @requires {Variable} $breakpoints\n///\n/// @example scss\n///  @include add-breakpoint(tvscreen, 1920px);\n///  @include mq(tvscreen) {}\n@mixin add-breakpoint($name, $width) {\n  $new-breakpoint: (\n    $name: $width,\n  );\n  $breakpoints: map.merge($breakpoints, $new-breakpoint) !global;\n  $breakpoints: _map-sort-by-value($breakpoints) !global;\n}\n\n/// Show the active breakpoint in the top right corner of the viewport\n/// @link https://github.com/sass-mq/sass-mq#seeing-the-currently-active-breakpoint\n///\n/// @param {List} $show-breakpoints [$show-breakpoints] - List of breakpoints to show in the top right corner\n/// @param {Map} $breakpoints [$breakpoints] - Breakpoint names and sizes\n///\n/// @requires {Variable} $breakpoints\n/// @requires {Variable} $show-breakpoints\n///\n/// @example scss\n///  // Show breakpoints using global settings\n///  @include show-breakpoints;\n///\n///  // Show breakpoints using custom settings\n///  @include show-breakpoints((L, XL), (S: 300px, L: 800px, XL: 1200px));\n@mixin show-breakpoints(\n  $show-breakpoints: $show-breakpoints,\n  $breakpoints: $breakpoints\n) {\n  body:before {\n    position: fixed;\n    top: 0;\n    right: 0;\n    z-index: 100;\n    padding: 3px 6px;\n    border-bottom: 1px solid #fbeed5;\n    border-left: 1px solid #fbeed5;\n    background-color: #fcf8e3;\n    color: #c09853;\n    pointer-events: none;\n\n    // Loop through the breakpoints that should be shown\n    @each $show-breakpoint in $show-breakpoints {\n      $width: get-breakpoint-width($show-breakpoint, $breakpoints);\n      @include mq($show-breakpoint, $breakpoints: $breakpoints) {\n        content: \"#{$show-breakpoint} ≥ #{$width} (#{px2em($width)})\";\n      }\n    }\n  }\n}\n\n@if list.length($show-breakpoints) > 0 {\n  @include show-breakpoints;\n}\n","$action-light-primary: #7145d6 !default;\n$action-light-secondary: #62628a !default;\n$action-light-negative: #994742 !default;\n$action-light-warning: #876c3c !default;\n$action-light-positive: #2a7b5c !default;\n$action-light-featured: #325e9f !default;\n$action-light-disabled: #e4e4f7 !default;\n$action-dark-primary: #b696ff !default;\n$action-dark-secondary: #adadd6 !default;\n$action-dark-negative: #ffa49e !default;\n$action-dark-warning: #ffd36b !default;\n$action-dark-positive: #8ae5c1 !default;\n$action-dark-featured: #84baff !default;\n$action-dark-disabled: #434366 !default;\n$background-light-base: #ffffff !default;\n$background-light-subdued: #f7f7fc !default;\n$background-dark-base: #1a1a38 !default;\n$background-dark-subdued: #262645 !default;\n$border-light-primary: #d2bfff !default;\n$border-light-secondary: #e4e4f7 !default;\n$border-light-negative: #ffc6c2 !default;\n$border-dark-primary: #5b39a8 !default;\n$border-dark-secondary: #434366 !default;\n$border-dark-negative: #994742 !default;\n$decoration-light-pink-default: #f55dae !default;\n$decoration-light-pink-subdued: #ffebf5 !default;\n$decoration-light-orange-default: #fc8549 !default;\n$decoration-light-orange-subdued: #fef0ea !default;\n$decoration-light-blue-default: #34bffa !default;\n$decoration-light-blue-subdued: #e5f7ff !default;\n$decoration-dark-pink-default: #ff9ed2 !default;\n$decoration-dark-pink-subdued: #633457 !default;\n$decoration-dark-orange-default: #ffbe9e !default;\n$decoration-dark-orange-subdued: #5f464e !default;\n$decoration-dark-blue-default: #85daff !default;\n$decoration-dark-blue-subdued: #36526b !default;\n$info-light-active: #7145d6 !default;\n$info-light-static: #8383ad !default;\n$info-light-negative: #cc524a !default;\n$info-light-warning: #c48f16 !default;\n$info-light-positive: #30a375 !default;\n$info-light-featured: #3979cc !default;\n$info-dark-active: #b696ff !default;\n$info-dark-static: #adadd6 !default;\n$info-dark-negative: #ffa49e !default;\n$info-dark-warning: #ffd36b !default;\n$info-dark-positive: #8ae5c1 !default;\n$info-dark-featured: #84baff !default;\n$state-light-primary-hover: #8c5cff !default;\n$state-light-primary-active: #5b39a8 !default;\n$state-light-secondary-hover: #8383ad !default;\n$state-light-secondary-active: #1a1a38 !default;\n$state-light-negative-hover: #fd665b !default;\n$state-light-negative-active: #994742 !default;\n$state-light-warning-hover: #f3af13 !default;\n$state-light-warning-active: #876c3c !default;\n$state-light-positive-hover: #41cc94 !default;\n$state-light-positive-active: #2a7b5c !default;\n$state-light-featured-hover: #4694fa !default;\n$state-light-featured-active: #325e9f !default;\n$state-dark-primary-hover: #d2bfff !default;\n$state-dark-primary-active: #f3edff !default;\n$state-dark-secondary-hover: #cbcbeb !default;\n$state-dark-secondary-active: #f2f2fa !default;\n$state-dark-negative-hover: #ffc6c2 !default;\n$state-dark-negative-active: #ffeceb !default;\n$state-dark-warning-hover: #ffe099 !default;\n$state-dark-warning-active: #fff5de !default;\n$state-dark-positive-hover: #b1eed5 !default;\n$state-dark-positive-active: #e6faf2 !default;\n$state-dark-featured-hover: #b1d3ff !default;\n$state-dark-featured-active: #e5f0ff !default;\n$surface-light-primary: #f3edff !default;\n$surface-light-secondary: #f2f2fa !default;\n$surface-light-negative: #ffeceb !default;\n$surface-light-warning: #fff5de !default;\n$surface-light-positive: #e6faf2 !default;\n$surface-light-featured: #e5f0ff !default;\n$surface-dark-primary: #372d5e !default;\n$surface-dark-secondary: #323252 !default;\n$surface-dark-negative: #5c3747 !default;\n$surface-dark-warning: #5c4f42 !default;\n$surface-dark-positive: #275252 !default;\n$surface-dark-featured: #294773 !default;\n$text-light-primary: #1a1a38 !default;\n$text-light-secondary: #62628a !default;\n$text-light-disabled: #8383ad !default;\n$text-dark-primary: #ffffff !default;\n$text-dark-secondary: #adadd6 !default;\n$text-dark-disabled: #8383ad !default;\n","@use \"@product/marshal/dist/styles/typography\";\n@use \"@product/marshal/dist/styles/layout\";\n@use \"@product/marshal/dist/styles/tokens/color\";\n@use \"@product/marshal/dist/styles/tools/mq\";\n\n$line-size: 2px;\n$line-gray: linear-gradient(to bottom, #cccccc, #cccccc);\n$line-black: linear-gradient(to bottom, #000000, #000000);\n\n$line-background-position: 7px calc(100% - 26px);\n$line-background-size: $line-size 100%;\n$line-background-no-size: $line-size 0;\n\n$line-background-position-mobile: 6px calc(100% - 11px);\n$line-background-size-mobile: calc(100% - 28px) $line-size;\n$line-background-no-size-mobile: 0 $line-size;\n\n$current-step-font-size: 16px;\n\n$stepIndicator_item-animation-duration: 1s;\n\n.stepIndicator_item {\n  @include typography.typography(\"body\");\n  display: flex;\n  align-items: flex-end;\n  margin-bottom: 2px;\n  min-height: 23px;\n  background-image:\n    // line pending grey\n    $line-gray,\n    // line done black\n    $line-black;\n  background-position:\n    // line pending grey\n    $line-background-position,\n    // line done black\n    $line-background-position;\n  background-size:\n    // line pending grey\n    $line-background-size,\n    // line done black\n    $line-background-no-size;\n  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;\n  cursor: pointer;\n\n  @include mq.mq($until: desktop) {\n    min-height: unset;\n    width: 100%;\n    background-image:\n      // line done black\n      $line-black,\n      // line pending grey\n      $line-gray;\n    background-position:\n      // line done black - mobile\n      $line-background-position-mobile,\n      // line pending grey - mobile\n      $line-background-position-mobile;\n    background-size:\n      // line done black - mobile\n      $line-background-no-size-mobile,\n      // line pending grey - mobile\n      $line-background-size-mobile;\n  }\n  &:first-child {\n    width: unset;\n    background-image: unset;\n  }\n\n  & + & {\n    min-height: 48px;\n\n    @include mq.mq($until: desktop) {\n      justify-content: flex-end;\n    }\n  }\n\n  &.stepIndicator_item-done,\n  &.stepIndicator_item-previous {\n    background-size:\n      // line pending grey\n      $line-background-no-size,\n      // line done black\n      $line-background-size;\n\n    @include mq.mq($until: desktop) {\n      background-size:\n        // line done black - mobile\n        $line-background-size-mobile,\n        // line pending grey - mobile\n        $line-background-size-mobile;\n    }\n  }\n\n  &-previous {\n    animation: stepIndicator_itemToPreviousState\n      $stepIndicator_item-animation-duration ease;\n    animation-fill-mode: forwards;\n\n    @include mq.mq($until: desktop) {\n      animation: unset;\n    }\n  }\n\n  &-current {\n    animation: stepIndicator_itemToCurrentState\n      $stepIndicator_item-animation-duration ease;\n    animation-fill-mode: forwards;\n\n    @include mq.mq($until: desktop) {\n      animation: stepIndicator_itemToCurrentStateMobile\n        $stepIndicator_item-animation-duration ease;\n      animation-fill-mode: forwards;\n    }\n  }\n\n  &_text {\n    @include mq.mq($until: desktop) {\n      text-indent: -9999px;\n    }\n  }\n}\n\n@keyframes stepIndicator_itemToPreviousState {\n  0% {\n    font-size: $current-step-font-size;\n  }\n  50% {\n    font-size: inherit;\n  }\n  100% {\n    font-size: inherit;\n  }\n}\n\n@keyframes stepIndicator_itemToCurrentState {\n  0% {\n    background-size:\n      // line pending grey\n      $line-background-size,\n      // line done black\n      $line-background-size;\n    font-size: inherit;\n  }\n  15% {\n    background-size:\n      // line pending grey\n      $line-background-size,\n      // line done black\n      $line-background-size;\n    font-size: inherit;\n  }\n  100% {\n    background-size:\n      // line pending grey\n      $line-background-no-size,\n      // line done black\n      $line-background-size;\n    font-size: $current-step-font-size;\n  }\n}\n\n@keyframes stepIndicator_itemToCurrentStateMobile {\n  0% {\n    background-size:\n      // line done black - mobile\n      $line-background-no-size-mobile,\n      // line pending grey - mobile\n      $line-background-size-mobile;\n  }\n  15% {\n    background-size:\n      // line done black - mobile\n      $line-background-no-size-mobile,\n      // line pending grey - mobile\n      $line-background-size-mobile;\n  }\n  100% {\n    background-size:\n      // line done black - mobile\n      $line-background-size-mobile,\n      // line pending grey - mobile\n      $line-background-size-mobile;\n  }\n}\n","@use \"sass:map\";\n@use \"./tokens/typography\";\n@use \"./tokens/typographyStyles\";\n@use \"./responsive\";\n@use \"./utils\";\n\n$font-weight-bold: 600;\n$font-weight-book: 400;\n$font-weight-light: 300;\n\n@mixin typography($style) {\n  $approach: utils.map-deep-get(\n    typographyStyles.$typographyStyles,\n    \"mq\",\n    responsive.$mq-approach\n  );\n\n  @each $mq-key, $mq-value in $approach {\n    $mq-from: map.get($mq-value, \"from\");\n    $mq-until: map.get($mq-value, \"until\");\n    $sizes: utils.map-deep-get(\n      typographyStyles.$typographyStyles,\n      \"sizes\",\n      $mq-key\n    );\n    $attributes: map-get(typographyStyles.$typographyStyles, \"attributes\");\n    @include responsive.mq($from: $mq-from, $until: $mq-until) {\n      @if map.has-key($attributes, $style) {\n        @each $attribute-key, $attribute-value in map-get($attributes, $style) {\n          #{$attribute-key}: $attribute-value;\n        }\n      }\n      @if map.has-key($sizes, $style) {\n        letter-spacing: utils.map-deep-get(\n          typography.$typography,\n          map.get($sizes, $style),\n          \"letter-spacing\"\n        );\n        font-size: utils.map-deep-get(\n          typography.$typography,\n          map.get($sizes, $style),\n          \"font-size\"\n        );\n        line-height: utils.map-deep-get(\n          typography.$typography,\n          map.get($sizes, $style),\n          \"line-height\"\n        );\n      }\n    }\n  }\n}\n","@use \"./StepIndicatorIcon\";\n@use \"./StepIndicatorItem\";\n@use \"@product/marshal/dist/styles/tools/mq\";\n\n.stepIndicator {\n  display: flex;\n  flex-direction: column;\n  list-style: none;\n\n  @include mq.mq($until: desktop) {\n    flex-direction: row;\n    justify-content: space-between;\n  }\n}\n","@use \"../layout\";\n@use \"../mixins\";\n@use \"../shadow\";\n@use \"../tokens/color\";\n@use \"../responsive\";\n\n.contextualPopup {\n  position: absolute;\n  z-index: 6;\n  margin-top: layout.$spacing;\n\n  &-content {\n    overflow: auto;\n    min-width: layout.$spacing * 26;\n    max-height: 500px;\n    border-radius: layout.$radius * 2;\n    background-color: color.$background-light-base;\n    box-shadow: shadow.$floating;\n  }\n\n  &-fixed {\n    position: fixed;\n  }\n}\n\n@include responsive.mq($until: tablet) {\n  .contextualPopup {\n    @include mixins.contextualPopup(\"contextualPopup-content\");\n\n    // states\n    &-content {\n      &.select-options {\n        padding: layout.$spacing * 2 layout.$spacing;\n        max-width: 100%;\n      }\n    }\n  }\n}\n","@use \"../../packages/marshal/src/styles/tokens/color\";\n\n.template {\n  &.is-errored {\n    color: color.$info-light-negative;\n  }\n}\n","@use \"../../packages/marshal/src/styles/tokens/color\";\n@use \"../../packages/marshal/src/styles/typography\";\n@use \"../../packages/marshal/src/styles/layout\";\n\n.sb-status {\n  @include typography.typography(\"heading4\");\n  width: 100%;\n  border-collapse: collapse;\n  // TODO: this shouldn't be a bg color (N800)\n  color: color.$background-dark-subdued;\n  text-align: left;\n\n  thead th {\n    padding-bottom: layout.$spacing;\n  }\n\n  tr {\n    > * {\n      padding: layout.$spacing;\n    }\n    &:not(:first-child) td {\n      border-top: solid 1px color.$surface-light-secondary;\n    }\n    td[class^=\"sb-status_state-\"] {\n      position: relative;\n      &:before {\n        content: \"\";\n        display: inline-block;\n        margin-right: layout.$spacing;\n        width: 10px;\n        height: 10px;\n        border-radius: 50%;\n      }\n\n      small {\n        font-size: 0.7em;\n      }\n    }\n  }\n  tr.sb-status-tabbed {\n    font-size: 0.9em;\n    td {\n      padding-top: layout.$spacing;\n      border-top: 0;\n\n      &:first-child {\n        padding-left: layout.$spacing * 3;\n      }\n    }\n  }\n\n  &_state-notConsidered:before {\n    box-shadow: inset 0 0 0 2px color.$info-light-warning;\n  }\n  &_state-underConsideration:before {\n    box-shadow: inset 0 0 0 2px color.$info-light-static;\n  }\n  &_state-inProgress:before {\n    // TODO: this shouldn't be a hover accent color (G500)\n    box-shadow: inset 0 0 0 2px color.$state-light-positive-hover;\n  }\n  &_state-done:before {\n    // TODO: this shouldn't be a hover accent color (G500)\n    background-color: color.$state-light-positive-hover;\n  }\n}\n","// TODO: include decoration colors in set if needed (LB500, P500)\n$color-blueLove: #8fceff; // LB500\n$color-hotPink: #ff85b6; // P500\n@use \"../../packages/marshal/src/styles/tokens/color\";\n@use \"../../packages/marshal/src/styles/typography\";\n\n.sb-typography_desktop {\n  .sb-typography_default {\n    background-color: rgba($color-blueLove, 0.16);\n    &:before {\n      color: $color-blueLove;\n    }\n  }\n  .sb-typography_valid {\n    &:before {\n      border-color: $color-blueLove;\n    }\n    &:after {\n      content: \"DESKTOP\";\n      color: $color-blueLove;\n    }\n  }\n}\n.sb-typography_mobile {\n  .sb-typography_default {\n    background-color: rgba($color-hotPink, 0.16);\n    &:before {\n      color: $color-hotPink;\n    }\n  }\n  .sb-typography_valid {\n    &:before {\n      border-color: $color-hotPink;\n    }\n    &:after {\n      content: \"MOBILE\";\n      color: $color-hotPink;\n    }\n  }\n}\n\n.sb-typography {\n  // TODO: this shouldn't be a background color (N800)\n  color: color.$background-dark-subdued;\n  &_size {\n    display: inline-block;\n    width: 50px;\n    color: color.$text-light-disabled;\n    text-align: right;\n  }\n  &_props {\n    color: color.$text-light-disabled;\n  }\n  &_style {\n    position: relative;\n    z-index: 1;\n    display: inline;\n    padding: 0 5px;\n    background-color: color.$background-light-base;