import type { HTMLProps } from 'react';

export const getHtmlProps = <Element extends HTMLElement, OwnProps>(
  props: OwnProps,
  ...ignoredProps: string[]
): HTMLProps<Element> => {
  const newProps = { ...props };
  ignoredProps.forEach((ignoredProp) => {
    delete newProps[ignoredProp];
  });
  return newProps as HTMLProps<Element>;
};
