import classnames from 'classnames';

import { StatusDataItem } from './StatusData';
import { StatusItemState } from './StatusItemState';

interface StatusItemProps {
  statusDataItem: StatusDataItem;
  tabbed?: boolean;
}

const StatusItem = ({ statusDataItem, tabbed }: StatusItemProps) => {
  const { name, designStatus, codeStatus, version } = statusDataItem;

  return (
    <tr className={classnames({ 'sb-status-tabbed': tabbed })}>
      <td>{name}</td>
      <StatusItemState statusState={designStatus} />
      <StatusItemState statusState={codeStatus} version={version} />
    </tr>
  );
};

export { StatusItem };
