import omit from 'lodash/omit';
import { forwardRef } from 'react';
import type { PolymorphicPropsWithRef } from 'react-polymorphic-types';

import { useLoadableButton } from '../hooks/useLoadableButton';

export type ButtonTypes =
  | 'primary'
  | 'secondary'
  | 'tertiary'
  | 'destructive'
  | 'none';

export type ButtonSizes = 'sm' | 'md';

export type ButtonDisplays = 'block' | 'full';

export type ButtonStatus = 'button' | 'is-submitting' | 'is-disabled';

export interface ButtonBaseProps {
  children?: React.ReactNode | React.FC<ButtonBaseProps>;
  className?: string;
  cssType?: ButtonTypes;
  disabled?: boolean;
  display?: ButtonDisplays;
  id?: string;
  onClick?: (event: React.MouseEvent<HTMLElement, MouseEvent>) => void;
  size?: ButtonSizes;
  submitting?: boolean;
  submittingOnClick?: boolean;
}

export const ButtonDefaultElement = 'button';

export interface ButtonOwnProps extends ButtonBaseProps {
  type?: 'button' | 'submit' | 'reset';
}

export type ButtonProps<
  ET extends React.ElementType = typeof ButtonDefaultElement,
> = PolymorphicPropsWithRef<ButtonOwnProps, ET>;

const SKIP_PROPS = [
  'cssType',
  'display',
  'size',
  'className',
  'submitting',
  'submittingOnClick',
];

const ForwardedRefButton = forwardRef(
  (
    {
      as,
      disabled = false,
      submitting = false,
      submittingOnClick = false,
      size = 'md',
      cssType = 'primary',
      type = 'button',
      ...givenProps
    }: ButtonProps<'button'>,
    ref: React.RefObject<HTMLButtonElement>,
  ) => {
    const props = {
      disabled,
      submitting,
      submittingOnClick,
      size,
      cssType,
      type,
      ...givenProps,
    } as ButtonProps<'button'>;

    const { className, onClick } = useLoadableButton(props);

    const otherProps = omit(props, ...SKIP_PROPS);

    const Element = as || ButtonDefaultElement;

    return (
      <Element
        className={className}
        data-marshalcomponent="Button"
        id={props.id}
        onClick={onClick}
        ref={ref}
        {...otherProps}
      >
        {props.children && typeof props.children === 'function'
          ? props.children(props)
          : props.children}
      </Element>
    );
  },
);

interface ButtonType {
  <ET extends React.ElementType = typeof ButtonDefaultElement>(
    props: ButtonProps<ET>,
  ): JSX.Element;
  displayName: string;
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const Button: ButtonType = ForwardedRefButton as any;

Button.displayName = 'Button';
