export interface TemplateProps {
  prop1?: string;
  error?: boolean;
}

export const Template = (props: TemplateProps) => {
  const className = 'template';

  return (
    <div
      className={props.error ? `${className} is-errored` : className}
      data-marshalcomponent="Template"
    >
      <span className={props.prop1}>Template component</span>
    </div>
  );
};

Template.displayName = 'Template';
Template.defaultProps = {};
