import classNames from 'classnames';
import { HTMLProps } from 'react';

import { StepIndicatorIcon } from './StepIndicatorIcon';
import { StepStatus } from './utils/inferStepStatus';

export interface StepIndicatorItemProps extends HTMLProps<HTMLLIElement> {
  children: string;
  stepStatus: StepStatus;
}

export const StepIndicatorItem = ({
  children,
  stepStatus,
}: StepIndicatorItemProps) => {
  return (
    <li
      className={classNames(
        'stepIndicator_item',
        `stepIndicator_item-${stepStatus}`,
      )}
    >
      <StepIndicatorIcon stepStatus={stepStatus} />
      <span className="stepIndicator_item_text">{children}</span>
    </li>
  );
};
