import { IcDirectionDownMd } from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { forwardRef, useRef } from 'react';
import mergeRefs from 'react-merge-refs';

import { Input } from '../Input';
import { OptionProps } from './Option';

type OptionChildren = React.ReactElement<OptionProps>;

export interface SelectBaseProps
  extends Omit<React.ComponentProps<typeof Input>, 'onChange' | 'onClick'> {
  errored?: boolean;
  onChange?: (val: string) => void;
  onClick?: () => void | React.MouseEventHandler<HTMLInputElement>;
  helper?: string | React.ReactNode;
  errorMessage?: string;
  isDropdownVisible?: boolean;
  visibleRef?: React.MutableRefObject<HTMLInputElement | null>;
  content: React.ReactNode | React.ReactNode[] | string;
  children?: OptionChildren | OptionChildren[] | null;
}

export const SelectInput = forwardRef(
  (
    {
      value,
      className,
      onChange,
      errored,
      helper,
      errorMessage,
      isDropdownVisible = false,
      visibleRef,
      content,
      children = null,
      ...restProps
    }: SelectBaseProps,
    ref: React.RefObject<HTMLInputElement>,
  ) => {
    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    return (
      <Input
        dataMarshalComponent="Select"
        {...restProps}
        className={classnames('formField-select', className, {
          'is-focused': isDropdownVisible,
        })}
        errored={errored}
        errorMessage={errorMessage}
        helper={helper}
        PostComponent={
          <>
            <div className="formField_input">{content}</div>
            <IcDirectionDownMd className="formField_icon formField_icon-dropdown" />
            {children}
          </>
        }
        ref={combinedRefs}
        tabIndex={-1}
        value={value}
        wrapperProps={{
          onClick: () => {
            restProps.onClick?.();
          },
          onKeyPress: () => {
            restProps.onClick?.();
          },
          tabIndex: 0,
          ref: visibleRef,
          className: restProps.wrapperProps?.className,
          'data-testid': 'selectWrapper',
        }}
      />
    );
  },
);
