import classnames from 'classnames';
import omit from 'lodash/omit';
import React, { forwardRef, useRef } from 'react';
import mergeRefs from 'react-merge-refs';

import { SelectionControlProps } from '../SelectionControl';

export interface RadioProps extends SelectionControlProps {
  value: string;
}

export const Radio = forwardRef(
  (props: RadioProps, ref: React.RefObject<HTMLInputElement>) => {
    const { labelClassName, className, label, description, id, disabled } =
      props;
    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    const otherProps = omit(props, ['description', 'label']);

    return (
      <div
        className={classnames('formField', 'formField-radio', className, {
          'is-disabled': disabled,
        })}
        data-marshalcomponent="Radio"
      >
        <div className="formField_control">
          <input
            {...otherProps}
            className="formField_radio"
            disabled={disabled}
            ref={combinedRefs}
            type="radio"
          />
          <label
            className={classnames('formField_label', labelClassName)}
            htmlFor={id}
          >
            {label}
            {description && (
              <p className="formField_description">{description}</p>
            )}
          </label>
        </div>
      </div>
    );
  },
);
