import { IcCircleCheck, IcCircleCross } from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { forwardRef } from 'react';

import { useDeprecated } from '../../../utils/useDeprecated';
import { Button, ButtonDisplays, ButtonProps, ButtonSizes } from './Button';

export interface SubmitButtonProps extends Omit<ButtonProps<'button'>, 'as'> {
  saveStatus?: SaveStatus;

  /**
   * @deprecated Use `display` instead.
   */
  cssDisplay?: ButtonDisplays;
  /**
   * @deprecated Use `children` instead.
   */
  buttonText?: string;
  /**
   * @deprecated Use `size` instead.
   */
  cssSize?: ButtonSizes;
}

export type SaveStatus = 'saving' | 'saved' | 'failed';

const Spinner = () => (
  <span className="buttonMotion_spinner" data-testid="spinner" />
);

const ButtonContent: React.FC<{ status?: SaveStatus }> = ({
  children,
  status,
}) => {
  const Icon = status === 'saved' ? IcCircleCheck : IcCircleCross;
  const statusClassName =
    status === 'saving'
      ? 'is-loading'
      : `buttonMotion-${status!} is-saved is-transitioning`;
  if (status) {
    return (
      <span className={classnames('buttonMotion', statusClassName)}>
        <span className="buttonMotion_before">
          {status === 'saving' ? children : <Icon />}
        </span>
        <span className="buttonMotion_after">
          <Spinner />
        </span>
      </span>
    );
  }

  return children as null;
};

export const SubmitButton = forwardRef(
  (
    {
      saveStatus,
      children,
      type = 'submit',
      disabled,
      className,

      buttonText,
      cssSize,
      cssDisplay,

      ...restProps
    }: SubmitButtonProps,
    ref: React.RefObject<HTMLButtonElement>,
  ) => {
    useDeprecated('SubmitButton', { buttonText, cssSize, cssDisplay });

    const classNames = classnames(className, {
      'is-blocked': saveStatus,
      'is-disabled': disabled,
    });

    return (
      <Button
        as="button"
        className={classNames}
        data-marshalcomponent="SubmitButton"
        disabled={disabled}
        display={cssDisplay}
        ref={ref}
        size={cssSize}
        type={type}
        {...restProps}
      >
        <ButtonContent status={saveStatus}>
          {children ?? buttonText}
        </ButtonContent>
      </Button>
    );
  },
);

SubmitButton.displayName = 'SubmitButton';
