import { IcLocationAlternate, Icons } from '@product/marshal-tokens';
import React, { forwardRef, useRef } from 'react';
import mergeRefs from 'react-merge-refs';

import { Option, Select, SelectProps } from '../Select';

interface PhoneCodeItem {
  code: string;
  prefix: string;
  name: string;
  selected?: boolean;
}

export interface PhoneCodeProps extends Omit<SelectProps, 'children'> {
  phoneCodes: PhoneCodeItem[];
}

export const PhoneCode = forwardRef(
  (
    { phoneCodes, ...props }: PhoneCodeProps,
    ref: React.RefObject<HTMLInputElement>,
  ) => {
    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    return (
      <Select
        {...props}
        className="formField-phoneCode"
        dataMarshalComponent="PhoneCode"
        ref={combinedRefs}
      >
        {phoneCodes.map((phoneCode) => {
          const flagCode = `${phoneCode.code.charAt(0)}${phoneCode.code
            .slice(1)
            .toLowerCase()}`;
          const Icon = Icons[`IcFlag${flagCode}`] || IcLocationAlternate;
          return (
            <Option
              key={phoneCode.code}
              selected={
                props.value
                  ? props.value === phoneCode.prefix
                  : phoneCode.selected
              }
              value={phoneCode.prefix}
            >
              <Icon className="select_icon" />
              <span className="select_value">{phoneCode.name}</span>
              <span className="select_prefix">
                {phoneCode.prefix ? `+${phoneCode.prefix}` : phoneCode.prefix}
              </span>
            </Option>
          );
        })}
      </Select>
    );
  },
);
