import classnames from 'classnames';
import type { ChangeEvent, ReactNode } from 'react';

interface SelectionControlsGroupProps {
  className?: string;
  helper?: string | ReactNode;
  errored?: boolean;
  errorMessage?: string;
  children: ReactNode | ReactNode[];
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
}

export const SelectionControlsGroup = ({
  className,
  children,
  helper,
  errored,
  errorMessage,
  onChange,
}: SelectionControlsGroupProps) => {
  return (
    <div
      className={classnames('formFieldGroup', className)}
      data-marshalcomponent="SelectionControlsGroup"
      onChange={onChange}
    >
      <div className="formField_wrapper">{children}</div>
      {errored && errorMessage && (
        <p className="formField_error">{errorMessage}</p>
      )}
      {helper && <p className="formField_helper">{helper}</p>}
    </div>
  );
};

SelectionControlsGroup.defaultProps = {
  className: '',
};
