import { useState } from 'react';

import { Switch, SwitchProps } from './index';

const BaseArgs = {
  id: 'switch_id',
  label: 'Label',
};

export const SwitchArgTypes = {};

const Base = (args: SwitchProps) => (
  <div className="test-zone">
    <Switch {...args} />
  </div>
);

export const CatalogStates = () => (
  <>
    <Switch id="selection_control1" label="Default" />
    <Switch checked id="selection_control2" label="Checked" />
    <Switch disabled id="selection_control3" label="Disabled" />
    <Switch
      checked
      disabled
      id="selection_control4"
      label="Disabled + checked"
    />
  </>
);;

export const SwitchControlled = () => {
  const [checked, setChecked] = useState(false);

  return (
    <>
      <Switch
        checked={false}
        id="2"
        label="Controlled"
        onChange={(e) => setChecked(e.target.checked)}
      />
      <Switch checked={checked} id="3" label="Controlled" />
    </>
  );
};;

export const SwitchPlayground = Base.bind({});;
SwitchPlayground.args = {
  ...BaseArgs,
  id: 'switch_1',
};

export const SwitchChecked = Base.bind({});;
SwitchChecked.args = {
  ...BaseArgs,
  id: 'switch_2',
  checked: true,
};

export const SwitchHelper = Base.bind({});;
SwitchHelper.args = {
  ...BaseArgs,
  id: 'switch_3',
  checked: true,
  helper: 'Helper text',
};

export const SwitchWithLabels = Base.bind({});;
SwitchWithLabels.args = {
  ...BaseArgs,
  id: 'switch_4',
  checkedLabel: 'Checked',
  uncheckedLabel: 'Unchecked',
};

export const SwitchDisabled = Base.bind({});;
SwitchDisabled.args = {
  ...BaseArgs,
  id: 'switch_5',
  disabled: true,
};

export const SwitchCheckedDisabled = Base.bind({});
SwitchCheckedDisabled.args = {
  ...BaseArgs,
  id: 'switch_6',
  checked: true,
  disabled: true,
};



