import classnames from 'classnames';
import React, { ElementType, forwardRef } from 'react';
import type { PolymorphicPropsWithRef } from 'react-polymorphic-types';

import { Button, ButtonOwnProps, ButtonProps } from './Button';

export interface BaseToggleButtonProps extends ButtonOwnProps {
  selected?: boolean;
}

export type ToggleButtonProps<Element extends ElementType = 'button'> =
  PolymorphicPropsWithRef<BaseToggleButtonProps, Element>;

const ForwardedRefToggleButton = forwardRef(
  <Element extends ElementType = 'button'>(
    {
      className,
      selected = false,
      children,
      size,
      as,
      ...props
    }: ToggleButtonProps<Element>,
    ref: ButtonProps['ref'],
  ) => {
    const classNames = classnames(
      { 'toggleButton-selected': selected, toggleButton: true },
      className,
    );
    return (
      <Button
        className={classNames}
        cssType="none"
        size={size}
        {...props}
        ref={ref}
      >
        {children}
      </Button>
    );
  },
);

interface ToggleButtonType {
  <ET extends React.ElementType = 'button'>(
    props: ToggleButtonProps<ET>,
  ): JSX.Element;
  displayName: string;
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const ToggleButton: ToggleButtonType = ForwardedRefToggleButton as any;
