import { IcCircleCheck, IcCloseMd } from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { useCallback, useEffect, useRef, useState } from 'react';

import { AvatarProps } from '../Avatar';
import { WhisperAvatar } from './WhisperAvatar';

export interface WhisperProps {
  variant?: 'success' | 'active' | 'accountSwitch';
  children: string;
  className?: string;
  dataTestId?: string;
  Avatar?: React.ReactElement<AvatarProps>;
  onClose?: (ref: React.RefObject<HTMLLIElement>) => void;
}

export const Whisper = ({
  variant = 'success',
  children,
  className,
  dataTestId,
  Avatar,
  onClose,
}: WhisperProps) => {
  const whisperRef = useRef() as React.MutableRefObject<HTMLLIElement>;
  const [statusClassName, setStatusClassName] = useState('notification-enter');
  const TIME_TO_CLOSE = 6000;
  const timer = useRef<number>();

  const handleClose = useCallback(() => {
    setStatusClassName('notification-exit');
    timer.current && window.clearTimeout(timer.current);
    window.setTimeout(() => {
      setStatusClassName('notification-remove');
      if (onClose) {
        onClose(whisperRef);
      }
    }, 500);
  }, [onClose]);

  useEffect(() => {
    timer.current = window.setTimeout(handleClose, TIME_TO_CLOSE);
    return () => {
      if (timer.current) window.clearTimeout(timer.current);
    };
  }, [handleClose, TIME_TO_CLOSE]);

  const dataTestIdByVariant = dataTestId || `whisper-${variant}`;

  const DESCRIPTOR_COMPONENT = {
    active: (
      <img
        alt="loading"
        className="whisper_icon whisper_typeDescriptor"
        src="https://assets.cabifil.es/images/progress_indicators/primary.png"
      />
    ),
    accountSwitch: Avatar,
    success: <IcCircleCheck className="whisper_icon whisper_typeDescriptor" />,
  };

  return (
    <li
      className={classnames(
        `notification whisper whisper-${variant}`,
        className,
        statusClassName,
      )}
      data-testid={dataTestIdByVariant}
      ref={whisperRef}
    >
      {DESCRIPTOR_COMPONENT[variant]}
      <div className="notification_description whisper_description">
        {children}
      </div>
      <div className="notification_actionIcon">
        <button aria-label="close" onClick={handleClose}>
          <IcCloseMd />
        </button>
      </div>
    </li>
  );
};

Whisper.WhisperAvatar = WhisperAvatar;
