import { typography, typographyStyles } from '@product/marshal-tokens';
import classNames from 'classnames';

const Style = ({ values, size }) => {
  const baseStyle = values.indexOf('body') >= 0 ? 'sb-typography_default' : '';
  const properties = Object.values(typography[size]);
  return (
    <p className={baseStyle} data-style={size}>
      <span className="sb-typography_style">
        <span className="sb-typography_size">{size}</span>
        <span className="sb-typography_styles">
          {values.map((v) => (
            <span
              className={classNames('sb-typography_title', `text-${v}`, {
                'sb-typography_featured': v === 'body',
              })}
              key={v}
            >
              {v}
            </span>
          ))}
        </span>
        <span className="sb-typography_props">{` - (${properties[0]}/${properties[1]})`}</span>
      </span>
    </p>
  );
};

function getStylesState(arr) {
  return arr.reduce(
    (acc, val) =>
      acc[val[1]]
        ? { ...acc, [val[1]]: [...acc[val[1]], val[0]] }
        : { ...acc, [val[1]]: [val[0]] },
    {},
  );
}

export const TypographyDesktop = () => {
  const desktopStyles = getStylesState(
    Object.entries(typographyStyles.sizes.desktop),
  );
  return (
    <div className="sb-typography_desktop">
      <div className="sb-typography_wrapper sb-typography_valid">
        {Object.entries(desktopStyles).map((desktop) => (
          <Style key={desktop[0]} size={desktop[0]} valid values={desktop[1]} />
        ))}
      </div>
    </div>
  );
};

export const TypographyMobile = () => {
  const mobileStyles = getStylesState(
    Object.entries(typographyStyles.sizes.mobile),
  );
  const { xxxxl, ...valid } = mobileStyles;

  return (
    <div className="sb-typography_mobile">
      <div className="sb-typography_wrapper">
        {Object.entries({ xxxxl }).map((invalid) => (
          <Style key={invalid[0]} size={invalid[0]} values={invalid[1]} />
        ))}
      </div>
      <div className="sb-typography_wrapper sb-typography_valid">
        {Object.entries(valid).map((mobile) => (
          <Style key={mobile[0]} size={mobile[0]} valid values={mobile[1]} />
        ))}
      </div>
    </div>
  );
};
