import classnames from 'classnames';
import { HTMLAttributes, useRef } from 'react';

import { TableCell } from './TableCell';
import { TableContext } from './TableContext';
import { TableHead } from './TableHead';
import { TableSummary } from './TableSummary';

export interface TableProps extends HTMLAttributes<HTMLDivElement> {
  hideHeaders?: boolean;
  columns?: number;
}

export const Table = ({
  className,
  hideHeaders,
  children,
  columns,
  role = 'table',
  ...props
}: TableProps) => {
  const tableRef = useRef<HTMLDivElement | null>(null);
  const defaultedColumns = columns ?? 1;
  return (
    <TableContext.Provider
      value={{ rowSize: defaultedColumns, inlineStyles: !!columns }}
    >
      <div
        data-marshalcomponent="Table"
        {...props}
        className={classnames('table', className, {
          'is-headerless': hideHeaders,
        })}
        ref={tableRef}
        role={role}
        style={
          columns ? { gridTemplateColumns: `repeat(${columns}, auto)` } : {}
        }
      >
        {children}
      </div>
    </TableContext.Provider>
  );
};

Table.Cell = TableCell;
Table.Head = TableHead;
Table.Summary = TableSummary;
