/* eslint-disable @typescript-eslint/naming-convention */
import cn from 'classnames';
import type { ElementType } from 'react';
import type { PolymorphicPropsWithoutRef } from 'react-polymorphic-types';

export type TypographyVariant =
  | 'heading0'
  | 'heading1'
  | 'heading2'
  | 'heading3'
  | 'heading4'
  | 'button'
  | 'bodylead'
  | 'heading5'
  | 'body'
  | 'heading6'
  | 'caption'
  | 'overline';

export type TypographyColor =
  | 'text-darkest'
  | 'text-darker'
  | 'text-dark'
  | 'text-light'
  | 'text-lighter'
  | 'text-lightest'
  | 'action-hard'
  | 'action-medium'
  | 'action-soft'
  | 'action-primary'
  | 'action-negative'
  | 'action-warning'
  | 'action-highlight';

export interface TypographyCommonProps {
  center?: boolean;
  color?: TypographyColor;
  ellipsis?: boolean;
}

export type TypographyOwnProps = TypographyCommonProps & {
  variant?: TypographyVariant;
};

const defaultElement = 'p';

export type TypographyProps<Element extends ElementType> =
  PolymorphicPropsWithoutRef<TypographyOwnProps, Element>;

const Typography = <Element extends ElementType = typeof defaultElement>({
  className,
  variant = 'body',
  color,
  ellipsis = false,
  center = false,
  as,
  ...restProps
}: TypographyProps<Element>) => {
  const RenderElement: ElementType = as || defaultElement;

  return (
    <RenderElement
      as={defaultElement}
      className={cn(className, {
        [`text-${variant}`]: variant,
        [`color-${color as NonNullable<TypographyColor>}`]: color,
        'text-ellipsis': ellipsis,
        'text-center': center,
      })}
      {...restProps}
    />
  );
};

export { Typography };
