import cn from 'classnames';
import * as React from 'react';
import type { PolymorphicPropsWithoutRef } from 'react-polymorphic-types';

import { Typography, TypographyCommonProps } from '../Typography';

export type OverlineOwnProps = TypographyCommonProps & {
  withDivider?: boolean;
};

export type OverlineProps<ElementType extends React.ElementType> =
  PolymorphicPropsWithoutRef<OverlineOwnProps, ElementType>;

const defaultElement = 'p';

const Overline = <
  ElementType extends React.ElementType = typeof defaultElement,
>({
  withDivider,
  className,
  ...restProps
}: OverlineProps<ElementType>) => (
  <Typography
    className={cn('overline', className, {
      [`overline-withDivider`]: withDivider,
    })}
    variant="overline"
    {...(restProps as TypographyCommonProps)}
  />
);

export { Overline };
