import { IcLock, IcOctagonCross, IcShieldCheck } from '@product/marshal-tokens';
import classnames from 'classnames';
import omit from 'lodash/omit';
import React, { forwardRef, useEffect, useRef, useState } from 'react';
import mergeRefs from 'react-merge-refs';

export type TextareaSizes = 'md';

export interface TextareaProps extends React.ComponentProps<'textarea'> {
  name: string;
  label: string;
  errorMessage?: string;
  errored?: boolean;
  validated?: boolean;
  helper?: string | React.ReactNode;
  testId?: string;
  size?: TextareaSizes;
  wrapperClassname?: string;
}

export const Textarea = forwardRef(
  (props: TextareaProps, ref: React.RefObject<HTMLTextAreaElement>) => {
    const {
      defaultValue,
      errored,
      errorMessage,
      placeholder,
      className,
      label,
      name,
      value,
      helper,
      disabled,
      validated,
      onBlur,
      onFocus,
      onChange,
      testId,
      size = 'md',
      wrapperClassname,
    } = props;
    const id = props.id || name;
    const textareaRef = useRef<HTMLTextAreaElement>(null);
    const [isFocused, setIsFocused] = useState(false);
    const [hasRefValue, setHasRefValue] = useState(false);
    const [isFirstRender, setIsFirstRender] = useState(true);
    const [controlledValue, setControlledValue] = useState(value);

    const combinedRefs = mergeRefs([textareaRef, ref]);

    const handleBlur = (e: React.FocusEvent<HTMLTextAreaElement>) => {
      setHasRefValue(!!textareaRef?.current?.value);
      setIsFocused(false);
      if (onBlur) {
        onBlur(e);
      }
    };

    const handleChange = React.useCallback(
      (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        if (value) setControlledValue(e.target.value);
        if (onChange) {
          onChange(e);
        }
      },
      [onChange, value],
    );

    useEffect(() => {
      setHasRefValue(!!textareaRef?.current?.value);
      setIsFirstRender(false);
      if (value) setControlledValue(value);
    }, [value]);

    const handleFocus = (e: React.FocusEvent<HTMLTextAreaElement>) => {
      setIsFocused(true);
      if (onFocus) {
        onFocus(e);
      }
    };

    const otherProps: React.ComponentProps<'textarea'> = omit(props, [
      'errored',
      'validated',
      'helper',
      'label',
      'errorMessage',
      'size',
      'wrapperClassname',
    ]);

    return (
      <div
        className={classnames(
          'formField',
          'formField-textarea',
          `formField-${size}`,
          className,
          {
            'is-validated': validated,
            'is-disabled': disabled,
            'is-errored': errored,
            'is-active':
              (isFirstRender && defaultValue) ||
              hasRefValue ||
              value ||
              placeholder ||
              isFocused,
            'is-focused': isFocused,
          },
        )}
        data-marshalcomponent="Textarea"
      >
        <div
          className={classnames(
            'formField_control',
            'formField_wrapper',
            wrapperClassname,
          )}
        >
          <label className="formField_placeholder" htmlFor={id}>
            {label}
          </label>
          <textarea
            {...otherProps}
            className="formField_textarea"
            data-testid={testId}
            name={name}
            onBlur={handleBlur}
            onChange={handleChange}
            onFocus={handleFocus}
            ref={combinedRefs}
            value={controlledValue}
          />
          {errored && (
            <IcOctagonCross className="formField_icon formField_icon-error" />
          )}
          {disabled && (
            <IcLock className="formField_icon formField_icon-disabled" />
          )}
          {validated && (
            <IcShieldCheck className="formField_icon formField_icon-validated" />
          )}
        </div>
        {errored && errorMessage && (
          <label className="formField_error" htmlFor={id}>
            {errorMessage}
          </label>
        )}
        {helper && <p className="formField_helper">{helper}</p>}
      </div>
    );
  },
);
