import {
  TabPanels as BaseTabPanels,
  TabPanelsProps as BaseTabPanelsProps,
} from '@reach/tabs';
import type * as Polymorphic from '@reach/utils/polymorphic';
import classnames from 'classnames';
import { ComponentProps } from 'react';

export type TabPanelsProps = ComponentProps<
  Polymorphic.MemoComponent<'div', BaseTabPanelsProps>
>;

const TabPanels = ({ className, ...props }: TabPanelsProps) => (
  <BaseTabPanels {...props} className={classnames('tabs_panels', className)} />
);

export { TabPanels };
