import { useState } from 'react';

import { Button } from './Button';
import { SaveStatus, SubmitButton, SubmitButtonProps } from './SubmitButton';

const SubmitButtonStory = ({
  display,
  cssType,
  saveStatus,
  onClick,
  children,
}: SubmitButtonProps) => (
  <SubmitButton
    cssDisplay={display}
    cssType={cssType}
    onClick={onClick}
    saveStatus={saveStatus}
  >
    {children}
  </SubmitButton>
);

export const IdleState = () => (
  <SubmitButton cssType="primary">Siguiente</SubmitButton>
);;

export const SubmitButtonPlayground = () => {
  const [saveStatus, setSaveStatus] = useState<SaveStatus>();
  const statusOK: SaveStatus[] = ['saving', 'saved'];
  const statusKO: SaveStatus[] = ['saving', 'failed'];

  function handleClick(status: SaveStatus[]) {
    let i = 0;
    setSaveStatus(status[i]);
    const interval = setInterval(() => {
      i += 1;
      if (i === status.length) {
        clearInterval(interval);
      }
      setSaveStatus(status[i]);
    }, 1500);
  }

  return (
    <>
      <Button
        cssType="secondary"
        onClick={() => handleClick(statusOK)}
        size="sm"
      >
        &quot;Save&quot; status
      </Button>
      <Button
        cssType="secondary"
        onClick={() => handleClick(statusKO)}
        size="sm"
      >
        &quot;Fail&quot; status
      </Button>
      <div className="sb-buttonMotion_example wip">
        <SubmitButtonStory cssType="primary" saveStatus={saveStatus}>
          Siguiente
        </SubmitButtonStory>
      </div>
    </>
  );
};



