import classnames from 'classnames';
import React, { useCallback, useEffect, useRef } from 'react';

export interface OptionProps {
  value?: string;
  selected?: boolean;
  initialValue?: string;
  className?: string;
  children: React.ReactNode | React.ReactNode[];
  onClick?: (value: string) => void;
  setFocus?: (value: number) => void;
  index?: number;
  focus?: boolean;
  initialRef?: React.MutableRefObject<HTMLDivElement | null>;
}

export const Option = ({
  className,
  children,
  onClick,
  value,
  setFocus,
  focus,
  index,
  initialValue,
  initialRef,
}: OptionProps) => {
  const ref = useRef<HTMLLIElement>(null);

  useEffect(() => {
    if (focus) {
      if (ref && ref.current) ref.current.focus();
    }
  }, [focus]);

  const handleSelect = useCallback(() => {
    onClick!(value!);
    setFocus!(index!);
    if (initialRef && initialRef.current) initialRef.current.focus();
  }, [index, initialRef, onClick, setFocus, value]);

  return (
    <li
      className={classnames(className, 'select_option', {
        'is-current': focus,
        'is-selected': initialValue === value,
      })}
      onClick={handleSelect}
      onKeyPress={handleSelect}
      ref={ref}
      tabIndex={focus ? 0 : -1}
    >
      {children}
    </li>
  );
};

Option.displayName = 'Option';
