import { IcCloseMd } from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { FC, useCallback, useEffect, useRef, useState } from 'react';

import { Banner, BannerProps } from './Banner';

export interface EmergingBannerProps extends BannerProps {
  onClose?: (ref: React.RefObject<HTMLLIElement>) => void;
}

const EmergingBanner: FC<EmergingBannerProps> = ({
  onClose,
  children,
  className,
  ...props
}) => {
  const [statusClassName, setStatusClassName] = useState('notification-enter');
  const bannerRef = useRef<HTMLLIElement>(null);
  const timeout = useRef<ReturnType<typeof setTimeout>>();

  const handleClose = useCallback(() => {
    setStatusClassName('notification-exit');
    timeout.current = setTimeout(() => {
      setStatusClassName('notification-remove');
      onClose?.(bannerRef);
    }, 500);
  }, [onClose]);

  useEffect(
    () => () => {
      timeout.current && clearTimeout(timeout.current);
    },
    [],
  );

  return (
    <Banner
      {...props}
      className={classnames('banner-emerging', statusClassName, className)}
      HtmlTagName="li"
      ref={bannerRef}
      trailingAction={
        <button aria-label="close" onClick={handleClose}>
          <IcCloseMd />
        </button>
      }
    >
      {children}
    </Banner>
  );
};

export { EmergingBanner };
