import { useState } from 'react';

import { SelectionControlsGroup } from '../../SelectionControlsGroup';
import { Checkbox, CheckboxProps } from './index';

const BaseArgs = {
  className: '',
  id: 'checkbox_id',
  label: 'Label',
};

export const CheckboxArgTypes = {};

const Base = (args: CheckboxProps) => (
  <div className="test-zone">
    <Checkbox {...args} />
  </div>
);

export const CatalogStates = () => (
  <>
    <Checkbox id="selection_control1" label="Default" />
    <Checkbox id="selection_control2" label="Hover" labelClassName=":hover" />
    <Checkbox defaultChecked id="selection_control3" label="Checked" />
    <Checkbox disabled id="selection_control4" label="Disabled" />
    <Checkbox
      defaultChecked
      disabled
      id="selection_control5"
      label="Disabled + checked"
    />
    <Checkbox errored id="selection_control6" label="Errored" />
  </>
);;

export const CheckboxPlayground = Base.bind({});;
CheckboxPlayground.args = {
  ...BaseArgs,
  id: 'checkbox_1',
};

export const CheckboxControlled = () => {
  const [checked, setChecked] = useState(false);

  return (
    <>
      <Checkbox
        defaultChecked={false}
        id="controlled1"
        label="Controlled"
        onChange={(e) => setChecked(e.target.checked)}
      />
      <Checkbox
        checked={checked}
        id="controlled2"
        label="Controlled (readonly)"
        readOnly
      />
    </>
  );
};;

export const CheckboxChecked = Base.bind({});;
CheckboxChecked.args = {
  ...BaseArgs,
  id: 'checkbox_checked',
  defaultChecked: true,
};

export const CheckboxUnchecked = Base.bind({});;
CheckboxUnchecked.args = {
  ...BaseArgs,
  id: 'checkbox_unchecked',
};

export const CheckboxError = Base.bind({});;
CheckboxError.args = {
  ...BaseArgs,
  id: 'checkbox_error',
  errored: true,
};

export const CheckboxCheckedDisabled = Base.bind({});;
CheckboxCheckedDisabled.args = {
  ...BaseArgs,
  id: 'checkbox_checked-disabled',
  defaultChecked: true,
  disabled: true,
};

export const CheckboxDisabled = Base.bind({});;
CheckboxDisabled.args = {
  ...BaseArgs,
  id: 'checkbox_disabled',
  disabled: true,
};

export const CheckboxGroup = () => (
  <SelectionControlsGroup>
    <Checkbox id="checkbox_1" label="Checkbox 1" />
    <Checkbox
      defaultChecked
      description="Helper message"
      id="checkbox_2"
      label="Checkbox 2"
    />
    <Checkbox id="checkbox_3" label="Checkbox 3" />
  </SelectionControlsGroup>
);;

export const CheckboxGroupHelpers = () => (
  <SelectionControlsGroup
    errored
    errorMessage="Error message"
    helper="Helper message"
  >
    <Checkbox id="checkbox_4" label="Checkbox 1" />
    <Checkbox
      defaultChecked
      description="Helper message"
      id="checkbox_5"
      label="Checkbox 2"
    />
    <Checkbox id="checkbox_6" label="Checkbox 3" />
  </SelectionControlsGroup>
);



