import phoneCodes from '../../../../../../storybook/PhoneCode/phoneCodesTemp.json';
import { PhoneCode, PhoneCodeProps } from './index';

const phoneCodesAll = phoneCodes.all.reduce(
  (acc, val) => {
    return phoneCodes.preferred.includes(val.code)
      ? { ...acc, preferred: [...acc.preferred, val] }
      : { ...acc, others: [...acc.others, val] };
  },
  {
    preferred: [],
    others: [],
  },
);

const BaseArgs = {
  name: 'phoneCode',
  label: 'Label',
  phoneCodes: [...phoneCodesAll.preferred, ...phoneCodesAll.others],
};

export const PhoneCodeArgTypes = {};

const Base = (args: PhoneCodeProps) => <PhoneCode {...args} />;

const BaseWithoutWrapper = (args: PhoneCodeProps) => (
  <div className="test-zone">
    <Base {...args} />
  </div>
);

const BaseWithWrapper = (args: PhoneCodeProps) => (
  <div className="test-zone sb-select_safeZone">
    <Base {...args} />
  </div>
);

export const PhoneCodePlayground = BaseWithoutWrapper.bind({});;
PhoneCodePlayground.args = {
  ...BaseArgs,
};

export const PhoneCodeExpanded = BaseWithWrapper.bind({});
PhoneCodeExpanded.args = {
  ...BaseArgs,
  expanded: true,
};



