import { useState } from 'react';

import { RangeSlider } from './RangeSlider';

const RangeSliderStory = ({
  label = 'The range slider title',
  min = 0,
  max = 100,
  step = 1,
  initialValues = [1, 2],
  description = 'Meaning of the range',
  unit = '',
  renderValue = (v: number, u: string) => `${v} ${u}`,
}) => {
  const [values, setValues] = useState<number[]>(initialValues);
  return (
    <RangeSlider
      description={description}
      label={label}
      max={max}
      min={min}
      onValuesChange={setValues}
      renderValue={renderValue}
      step={step}
      unit={unit}
      values={values as [number, number]}
    />
  );
};

export const RangeSliderPlayground = () => (
  <RangeSliderStory
    description="The range description"
    initialValues={[15, 75]}
    label="label"
    max={100}
    unit="%"
  />
);;

export const RangeSliderWithCustomFormat = () => (
  <RangeSliderStory
    description="This range slider shows labels with a custom format"
    initialValues={[10000, 45000]}
    label="label"
    max={50000}
    renderValue={(cents: number, unit: string) => {
      const formatter = new Intl.NumberFormat('de-DE', {
        style: 'currency',
        currency: unit,
      });
      return formatter.format(cents / 100);
    }}
    step={25}
    unit="EUR"
  />
);;

export const RangeSliderWithStep = () => (
  <RangeSliderStory
    description="This range slider grows using increments of 5."
    initialValues={[15, 45]}
    label="Time of last trip"
    max={90}
    renderValue={(value, unit) => `${value} ${unit}`}
    step={5}
    unit="days"
  />
);;

export const RangeSliderWithMinimum = () => (
  <RangeSliderStory
    description="This range slider has a minimum value of -25."
    initialValues={[10, 20]}
    label="Price variation"
    max={25}
    min={-25}
    unit="%"
  />
);



