import {
  ActionButton,
  Col,
  Grid,
  IcCopy,
  IcDownload,
  Option,
  Search,
  Select,
  Tabs,
  useComponentVisible,
  // eslint-disable-next-line import/no-unresolved
} from '@product/marshal';
import classNames from 'classnames';
import { useState } from 'react';

import { copyToClipboard } from './copyToClipboard';
import { downloadResource } from './downloadResource';

const RenderAsset = ({ icon, name }) => {
  const { visibleRef, isComponentVisible, setIsComponentVisible } =
    useComponentVisible(false);
  const path = `https://assets.cabifil.es/images/${name}/${icon}`;
  return (
    <li
      className="sb-icons_item"
      key={icon}
      onClick={() => setIsComponentVisible(!isComponentVisible)}
      ref={visibleRef}
    >
      <figure className="sb-icons_wrapper">
        <img
          alt={icon}
          className="sb-icons_placeholderLight"
          onError={(e) => {
            e.target.onerror = null;
            e.target.src = 'images/bad.svg';
          }}
          src={path}
        />
        <figcaption className="sb-icons_desc">
          {icon.replace('ic_', '').replace('.svg', '')}
        </figcaption>
      </figure>
      {isComponentVisible && (
        <div className="contextualPopup">
          <div className="contextualPopup-content">
            <ul>
              <li>
                <ActionButton
                  icon={IcCopy}
                  onClick={() => copyToClipboard(path)}
                  size="sm"
                >
                  Copy URL
                </ActionButton>
              </li>
              <li>
                <ActionButton
                  icon={IcDownload}
                  onClick={() => downloadResource(path)}
                  size="sm"
                >
                  Download SVG
                </ActionButton>
              </li>
            </ul>
          </div>
        </div>
      )}
    </li>
  );
};

export const AssetsWrapper = (IconsArrayObject, className = '') => {
  const [iconsArray, setIconsArray] = useState(IconsArrayObject);
  const [surfaceClassName, setSurfaceClassName] = useState('lightMode');

  const handleChangeSearch = (val) => {
    const filteredIcons = Object.entries(IconsArrayObject).reduce(
      (acc, [k, v]) => ({ ...acc, [k]: v.filter((i) => i.indexOf(val) >= 0) }),
      {},
    );
    setIconsArray({ ...filteredIcons });
  };

  const handleChangeSelect = (val) => {
    setSurfaceClassName(val);
  };

  return (
    <div className={classNames('sb-icons', className)}>
      <Grid className="sb-icons_filters">
        <Col mobile={6}>
          <Search
            label="Search for icons"
            onChange={handleChangeSearch}
            size="sm"
            value=""
          />
        </Col>
        <Col mobile={6}>
          <Select label="Surface" onChange={handleChangeSelect} size="sm">
            <Option value="lightMode">Light mode</Option>
            <Option value="darkMode">Dark mode</Option>
          </Select>
        </Col>
      </Grid>
      <Tabs>
        <Tabs.TabList>
          {Object.entries(iconsArray).map(([k, v]) => (
            <Tabs.Tab key={k}>{`${k} (${v.length})`}</Tabs.Tab>
          ))}
        </Tabs.TabList>
        <Tabs.Panels className={classNames(surfaceClassName)}>
          {Object.entries(iconsArray).map(([key, values]) => (
            <Tabs.Panel key={key}>
              <ul className="sb-icons_list sb-icons_catalog">
                {values.map((v) => (
                  <RenderAsset icon={v} key={v} name={key} />
                ))}
              </ul>
            </Tabs.Panel>
          ))}
        </Tabs.Panels>
      </Tabs>
    </div>
  );
};
