import { IconProps } from '@product/marshal-tokens';

import { Avatar, AvatarProps } from '../Avatar';

interface WhisperAvatarProps extends AvatarProps {
  Icon?: React.ComponentType<IconProps>;
}

export function WhisperAvatar({ Icon, ...props }: WhisperAvatarProps) {
  return (
    <div className="whisper_avatar">
      <Avatar {...props} size="md" />
      {Icon && (
        <div className="whisper_avatarIconWrapper">
          <Icon className="whisper_avatarIcon" />
        </div>
      )}
    </div>
  );
}
