import { StatusState } from './StatusData';

const STATUS_STATE_VALUE: Record<StatusState, string> = {
  IN_PROGRESS: 'In progress',
  UNDER_CONSIDERATION: 'Under consideration',
  DONE: 'Done',
  NOT_CONSIDERED: 'Not considered',
};

const STATUS_STATE_CLASSNAME: Record<StatusState, string> = {
  IN_PROGRESS: 'inProgress',
  UNDER_CONSIDERATION: 'underConsideration',
  DONE: 'done',
  NOT_CONSIDERED: 'notConsidered',
};

interface StatusStateProps {
  statusState: StatusState;
  version?: string;
}

const StatusItemState = ({ statusState, version }: StatusStateProps) => {
  const value = STATUS_STATE_VALUE[statusState];
  const classname = STATUS_STATE_CLASSNAME[statusState];
  const isDoneWithVersion = statusState === StatusState.DONE && version;
  return (
    <td className={`sb-status_state-${classname}`}>
      {value}
      {isDoneWithVersion && (
        <>
          {' '}
          <small>
            since <strong>{version}</strong>
          </small>
        </>
      )}
    </td>
  );
};

export { StatusItemState };
