import { useState } from 'react';

import {
  MultiSelect,
  MultiSelectOption,
  MultiSelectProps,
} from './MultiSelect';

const values = [
  'apple',
  'melon',
  'watermelon',
  'lemon',
  'pear',
  'banana',
  'orange',
];

const buildOptions = (amount: number): MultiSelectOption[] => {
  const items = [];

  for (let i = 0; i < amount; i++) {
    const value = values[i] || `item${i}`;
    items.push({ key: `item-${value}`, value });
  }

  return items;
};

const BaseArgs = {
  name: 'select',
  options: buildOptions(7),
  literals: {
    selectLabel: 'label',
    applyLabel: 'Apply',
    cancelLabel: 'Cancel',
    emptyOptionsText: 'No available options selected',
    selectPlaceholder: 'Placeholder',
    searchInputLabel: 'Search',
    selectAllLabel: 'Select All',
  },
  selectedKeys: [],
};

const Base = (args: MultiSelectProps) => {
  const [selectedKeys, setSelectedKeys] = useState<string[]>(args.selectedKeys);
  const [terms, setTerms] = useState<string>('');

  return (
    <>
      <p>{args.onCustomSearch ? `Searching: ${terms}` : undefined}</p>

      <MultiSelect
        {...args}
        onCustomSearch={args.onCustomSearch ? setTerms : undefined}
        onSetSelectedKeys={setSelectedKeys}
        selectedKeys={selectedKeys}
      />
    </>
  );
};

const BaseWithoutWrapper = (args: MultiSelectProps) => {
  return (
    <div className="test-zone">
      <Base {...args} />
    </div>
  );
};

const BaseWithWrapper = (args: MultiSelectProps) => {
  return (
    <div className="test-zone sb-multiSelect_safeZone">
      <Base {...args} />
    </div>
  );
};

export const MultiSelectPlayground = BaseWithoutWrapper.bind({});;
MultiSelectPlayground.args = {
  ...BaseArgs,
  selectedKeys: ['item-apple', 'item-lemon'],
};

export const MultiSelectEmpty = BaseWithWrapper.bind({});;
MultiSelectEmpty.args = {
  ...BaseArgs,
  expanded: true,
  options: [],
};

export const MultiSelectExpanded = BaseWithWrapper.bind({});;
MultiSelectExpanded.args = {
  ...BaseArgs,
  expanded: true,
};

export const MultiSelectCustomSearch = BaseWithWrapper.bind({});;
MultiSelectCustomSearch.args = {
  onCustomSearch: () => null,
  ...BaseArgs,
  expanded: true,
};

export const MultiSelectWithTooltip = BaseWithoutWrapper.bind({});
MultiSelectWithTooltip.args = {
  ...BaseArgs,
  withTooltip: true,
  selectedKeys: ['item-apple', 'item-lemon'],
};



