import { StepIndicatorItem } from './StepIndicatorItem';
import { StepIndicatorList } from './StepIndicatorList';
import { inferStepStatus } from './utils/inferStepStatus';

export interface StepIndicatorProps {
  stepsLabels: string[];
  currentStepIndex?: number;
  className?: string;
}

export const StepIndicator = ({
  stepsLabels,
  currentStepIndex = 0,
  className,
}: StepIndicatorProps) => (
  <StepIndicatorList className={className}>
    {stepsLabels.map((stepLabel, index) => {
      const stepStatus = inferStepStatus({
        currentStepIndex,
        stepIndex: index,
      });
      return (
        <StepIndicatorItem key={stepLabel} stepStatus={stepStatus}>
          {stepLabel}
        </StepIndicatorItem>
      );
    })}
  </StepIndicatorList>
);

StepIndicator.List = StepIndicatorList;
StepIndicator.Item = StepIndicatorItem;
