import { noop } from '@product/utils-drawer';
import round from 'lodash/round';
import uniqueId from 'lodash/uniqueId';
import React, { useMemo } from 'react';

export interface RangeSliderProps {
  label: string;
  description?: string;
  min?: number;
  max: number;
  step?: number;
  values: [number, number];
  inputNames?: [string, string];
  unit?: string;
  renderValue?: (value: number, unit: string) => string;
  onValuesChange?: (values: [number, number]) => void;
  disabled?: boolean;
}

interface Measures {
  leftPercentage: number;
  rightPercentage: number;
  progressBarWidth: number;
}

const getMeasures = (
  min: number,
  max: number,
  leftValue: number,
  rightValue: number,
): Measures => {
  const fullBarValue = max - min;
  const toPercentage = (amount: number) =>
    round((100 * (amount - min)) / fullBarValue, 4);
  const leftPercentage = toPercentage(leftValue);
  const rightPercentage = toPercentage(rightValue);
  const progressBarWidth = rightPercentage - leftPercentage;

  return {
    leftPercentage,
    rightPercentage,
    progressBarWidth,
  };
};

const defaultRenderValue: RangeSliderProps['renderValue'] = (value, unit) =>
  `${value} ${unit}`;

const SLIDER_THUMB_SIZE = 16;
const TOOLTIP_WRAPPER_SIZE = 50;

export const RangeSlider = ({
  label,
  description,
  min = 0,
  max,
  step = 1,
  values,
  inputNames,
  unit = '',
  renderValue = defaultRenderValue,
  onValuesChange = noop,
  disabled = false,
}: RangeSliderProps) => {
  const [leftValue, rightValue] = values;
  const [rangeId, leftValueId, rightValueId] = useMemo(
    () => [
      uniqueId('rangeSlider'),
      uniqueId('leftRangeInput'),
      uniqueId('rightRangeInput'),
    ],
    [],
  );

  const [leftInputName, rightInputName] = inputNames || [
    leftValueId,
    rightValueId,
  ];

  const { leftPercentage, rightPercentage, progressBarWidth } = getMeasures(
    min,
    max,
    leftValue,
    rightValue,
  );

  const progressStyle = {
    left: `${leftPercentage}%`,
    width: `${progressBarWidth}%`,
  };

  const progressBarCorrection = (percent: number) =>
    `${(SLIDER_THUMB_SIZE * (TOOLTIP_WRAPPER_SIZE - percent)) / 100}px`;

  const leftLabelStyle = {
    left: `calc(${leftPercentage}% + ${progressBarCorrection(leftPercentage)})`,
  };

  const rightLabelStyle = {
    left: `calc(${rightPercentage}% + ${progressBarCorrection(
      rightPercentage,
    )})`,
  };

  const handleLeftValueChange = (e: React.FormEvent<HTMLInputElement>) => {
    const value = parseInt(e.currentTarget.value, 10);
    if (value >= rightValue) {
      return;
    }
    onValuesChange([value, rightValue]);
  };

  const handleRightValueChange = (e: React.FormEvent<HTMLInputElement>) => {
    const value = parseInt(e.currentTarget.value, 10);
    if (value <= leftValue) {
      return;
    }
    onValuesChange([leftValue, value]);
  };

  return (
    <div
      className="formField formField-slider"
      data-marshalcomponent="RangeSlider"
    >
      <div className="formField_control">
        <div className="formField_label" id={rangeId}>
          {label}
        </div>
        <div className="formField_reference">
          <span>{renderValue(min, unit)}</span>
          <span>{renderValue(max, unit)}</span>
        </div>
        <div className="formField_sliderBox">
          <span className="formField_progressLine" style={progressStyle} />
          <input
            className="formField_sliderInput"
            disabled={disabled}
            id={leftValueId}
            max={max}
            min={min}
            name={leftInputName}
            onChange={handleLeftValueChange}
            step={step}
            type="range"
            value={leftValue}
          />
          <label
            className="formField_thumbLabel"
            htmlFor={leftValueId}
            style={leftLabelStyle}
          >
            <span className="formField_thumbValue">
              {renderValue(leftValue, unit)}
            </span>
          </label>
          <input
            className="formField_sliderInput"
            disabled={disabled}
            id={rightValueId}
            max={max}
            min={min}
            name={rightInputName}
            onChange={handleRightValueChange}
            step={step}
            type="range"
            value={rightValue}
          />
          <label
            className="formField_thumbLabel"
            htmlFor={rightValueId}
            style={rightLabelStyle}
          >
            <span className="formField_thumbValue">
              {renderValue(rightValue, unit)}
            </span>
          </label>
        </div>
        {description && <p className="formField_description">{description}</p>}
      </div>
    </div>
  );
};

RangeSlider.displayName = 'RangeSlider';
