import { FC } from 'react';

import { Tooltip } from '../../Tooltip';
import { MultiSelectOption } from './MultiSelect';
import { MultiSelectTooltipItem } from './MultiSelectTooltipItem';

export interface TooltipContainerProps {
  selectedOptions: MultiSelectOption[];
  showTooltip?: boolean;
}

const MultiSelectTooltipContainer: FC<TooltipContainerProps> = ({
  children,
  selectedOptions,
  showTooltip = false,
}) => {
  const shouldShowTooltip = showTooltip && selectedOptions.length > 1;

  return shouldShowTooltip ? (
    <Tooltip
      placement="left"
      title={
        <ul className="multiSelectTooltip">
          {selectedOptions.map((option) => {
            return (
              <MultiSelectTooltipItem key={option.key}>
                {option.value}
              </MultiSelectTooltipItem>
            );
          })}
        </ul>
      }
    >
      {children}
    </Tooltip>
  ) : (
    <>{children}</>
  );
};

export { MultiSelectTooltipContainer };
