import {
  IcCloseMd,
  IcDirectionDownMd,
  IcMagicWand,
} from '@product/marshal-tokens';
import { noop } from '@product/utils-drawer';
import classnames from 'classnames';
import { useState } from 'react';

import { Banner, BannerProps } from './Banner';
import { EmergingBanner } from './EmergingBanner';

const BaseArgs = {
  children: 'Lorem ipsum',
  inlineAction: <button onClick={noop}>Button with long text</button>,
};

export const BannerArgTypes = {};

const Base = (args: BannerProps) => (
  <div className="test-zone sb-notification">
    <Banner {...args} />
  </div>
);

export const BannerPlayground = Base.bind({});;
BannerPlayground.args = {
  ...BaseArgs,
};

export const BannerPositive = Base.bind({});;
BannerPositive.args = {
  ...BaseArgs,
  variant: 'positive',
};

export const BannerNegative = Base.bind({});;
BannerNegative.args = {
  ...BaseArgs,
  variant: 'negative',
};

export const BannerWarning = Base.bind({});;
BannerWarning.args = {
  ...BaseArgs,
  variant: 'warning',
};

export const BannerFeatured = Base.bind({});;
BannerFeatured.args = {
  ...BaseArgs,
  variant: 'featured',
};

export const BannerPromotion = Base.bind({});;
BannerPromotion.args = {
  ...BaseArgs,
  variant: 'promotion',
};

export const BannerCustomIcon = Base.bind({});;
BannerCustomIcon.args = {
  ...BaseArgs,
  icon: IcMagicWand,
};

export const BannerFull = Base.bind({});;
BannerFull.args = {
  ...BaseArgs,
  title: 'Title',
  blockActions: (
    <>
      <button key="action1">Action 1</button>
      <button key="action2">Action 2</button>
    </>
  ),
};

export const BannerEmerging = () => (
  <div className="test-zone sb-notification sb-notification-big">
    <ul className="sb-notification_placeholder">
      <EmergingBanner
        {...BaseArgs}
        blockActions={
          <>
            <button key="action1">Action 1</button>
            <button key="action2">Action 2</button>
          </>
        }
        title="Title"
      />
    </ul>
  </div>
);;

export const BannerEnrichedText = Base.bind({});;
BannerEnrichedText.args = {
  ...BaseArgs,
  children: (
    <>
      Lorem ipsum <span className="is-highlighted">dolor sit amet</span>,
      consectetur adipiscing elit. Proin arcu nibh, facilisis eu auctor in,
      tristique efficitur enim. Pellentesque placerat ultricies dolor, vel
      interdum ipsum cursus vitae. Proin varius semper porttitor. Duis eu ex eu
      libero tempus lobortis. Mauris efficitur ut odio ut venenatis. Vivamus
      laoreet metus non diam malesuada sagittis
    </>
  ),
};

export const BannerTrailingIcon = () => {
  const [isHidden, setIsHidden] = useState(false);

  return (
    <Banner
      className={classnames('sb-banner', {
        'is-hidden': isHidden,
      })}
      trailingAction={
        <IcCloseMd
          onClick={() => {
            setIsHidden(true);
          }}
        />
      }
    >
      Lorem ipsum
    </Banner>
  );
};;

export const BannerTrailingIcon2 = () => {
  const [isCollapsed, setIsCollapsed] = useState(true);
  return (
    <Banner
      className={classnames('sb-banner', {
        'is-collapsed': isCollapsed,
      })}
      trailingAction={
        <IcDirectionDownMd
          onClick={() => {
            setIsCollapsed(!isCollapsed);
          }}
        />
      }
    >
      <>
        Lorem ipsum dolor sit amet consectetur adipiscing elit. Proin arcu nibh,
        facilisis eu auctor in, tristique efficitur enim. Pellentesque placerat
        ultricies dolor, vel interdum ipsum cursus vitae. Proin varius semper
        porttitor. Duis eu ex eu libero tempus lobortis. Mauris efficitur ut
        odio ut venenatis. Vivamus laoreet metus non diam malesuada sagittis
        <div className="sb-banner_collapsible">
          This content can be hidden by clicking the trailing icon
        </div>
      </>
    </Banner>
  );
};



