import { IcCloseMd } from '@product/marshal-tokens';
import { useState } from 'react';

import { Button } from '../Button';
import { Modal, ModalProps } from './Modal';

export const ModalArgTypes = {};

const BaseArgs = {
  attachTo: null,
  closeIcon: <IcCloseMd />,
};

const Base = (args: ModalProps) => {
  const [isModalVisible, setIsModalVisible] = useState(true);

  return (
    <div className="test-zone modal_wrapper" id="modalStory">
      <Button onClick={() => setIsModalVisible(!isModalVisible)} size="sm">
        Show Modal
      </Button>
      {isModalVisible && (
        <Modal onCloseModal={() => setIsModalVisible(false)} {...args}>
          <p>This is a modal</p>
        </Modal>
      )}
    </div>
  );
};

export const ModalPlayground = Base.bind({});;
ModalPlayground.args = {
  ...BaseArgs,
};

export const ModalLeft = Base.bind({});;
ModalLeft.args = {
  ...BaseArgs,
  position: 'left',
};

export const ModalRight = Base.bind({});;
ModalRight.args = {
  ...BaseArgs,
  position: 'right',
};

export const ModalTop = Base.bind({});;
ModalTop.args = {
  ...BaseArgs,
  position: 'top',
};

export const ModalBottom = Base.bind({});
ModalBottom.args = {
  ...BaseArgs,
  position: 'bottom',
};



