import {
  IcColorVehicleMotorbike,
  IcLocationAlternate,
} from '@product/marshal-tokens';
import { ReactNode } from 'react';

import { Option, Select, SelectProps } from './index';

const dataOptions = [
  { value: 'one', content: 'One' },
  {
    value: 'two',
    content: <span>React node option</span>,
  },
  { value: 'three', content: 'Three' },
];

interface SelectCustomProps extends SelectProps {
  data: { value: string; content: ReactNode; className?: 'string' }[];
}

const BaseArgs = {
  name: 'select',
  label: 'Label',
  data: dataOptions,
};

export const SelectArgTypes = {};

const Base = (args: SelectCustomProps) => (
  <Select {...args}>
    {args.data.map((d) => (
      <Option className={d.className} key={d.value} value={d.value}>
        {d.content}
      </Option>
    ))}
  </Select>
);

const BaseWithoutWrapper = (args: SelectCustomProps) => (
  <div className="test-zone">
    <Base {...args} />
  </div>
);

const BaseWithWrapper = (args: SelectCustomProps) => (
  <div className="test-zone sb-select_safeZone">
    <Base {...args} />
  </div>
);

export const SelectPlayground = BaseWithoutWrapper.bind({});;
SelectPlayground.args = {
  ...BaseArgs,
};

export const SelectHelper = BaseWithoutWrapper.bind({});;
SelectHelper.args = {
  ...BaseArgs,
  helper: 'Helper message',
};

export const SelectExpanded = BaseWithWrapper.bind({});;
SelectExpanded.args = {
  ...BaseArgs,
  expanded: true,
};

export const SelectDisabled = BaseWithoutWrapper.bind({});;
SelectDisabled.args = {
  ...BaseArgs,
  disabled: true,
};

export const SelectErrored = BaseWithoutWrapper.bind({});;
SelectErrored.args = {
  ...BaseArgs,
  errored: true,
  errorMessage: 'Error message',
};

export const SelectIcons = BaseWithoutWrapper.bind({});;
SelectIcons.args = {
  ...BaseArgs,
  data: [
    { value: 'two', content: 'Two' },
    {
      value: 'one',
      content: (
        <>
          <IcColorVehicleMotorbike className="select_icon" />
          <span>Icon option</span>
        </>
      ),
    },
    { value: 'three', content: 'Three' },
  ],
};

const CatalogStates = (
  props: Omit<SelectCustomProps, 'name' | 'children' | 'data' | 'label'>,
) => {
  return (
    <>
      <div>
        <Select {...BaseArgs} {...props}>
          <Option value="option">Option</Option>
        </Select>
      </div>
      <div>
        <Select
          {...BaseArgs}
          {...props}
          LeadingIcon={IcLocationAlternate}
          size="sm"
        >
          <Option value="option">Option</Option>
        </Select>
      </div>
      <div>
        <Select
          {...BaseArgs}
          {...props}
          LeadingIcon={IcLocationAlternate}
          size="sm"
          variant="quiet"
        >
          <Option value="option">Option</Option>
        </Select>
      </div>
    </>
  );
};

