import { Tab as BaseTab, TabProps as BaseTabProps } from '@reach/tabs';
import type { ForwardRefComponent } from '@reach/utils/polymorphic';
import classnames from 'classnames';
import { forwardRef } from 'react';

export type TabProps = BaseTabProps;

export const Tab: ForwardRefComponent<'button', TabProps> = forwardRef(
  ({ className, ...props }, ref) => (
    <BaseTab
      {...props}
      className={classnames('tabs_tab', className)}
      ref={ref}
    />
  ),
);
