import {
  TabPanel as BaseTabPanel,
  TabPanelProps as BaseTabPanelProps,
} from '@reach/tabs';
import type { ForwardRefComponent } from '@reach/utils/polymorphic';
import classnames from 'classnames';
import { forwardRef } from 'react';

export type TabPanelProps = BaseTabPanelProps;

const TabPanel: ForwardRefComponent<'div', TabPanelProps> = forwardRef(
  ({ className, ...props }, ref) => (
    <BaseTabPanel
      {...props}
      className={classnames('tabs_panel', className)}
      ref={ref}
    />
  ),
);

export { TabPanel };
