import {
  IcLock,
  IcOctagonCross,
  IconProps,
  IcShieldCheck,
} from '@product/marshal-tokens';
import classnames from 'classnames';
import omit from 'lodash/omit';
import React, {
  forwardRef,
  PropsWithRef,
  useEffect,
  useRef,
  useState,
} from 'react';
import mergeRefs from 'react-merge-refs';

interface InputWrapperProps {
  onClick?: (event: React.MouseEvent) => void;
  onKeyPress?: (event: React.KeyboardEvent<HTMLDivElement>) => void;
  tabIndex?: number;
  ref?: React.MutableRefObject<HTMLInputElement | null>;
  className?: string;
  'data-testid'?: string;
}

export type InputSizes = 'sm' | 'md';
export type InputVariants = 'quiet';
interface InputOwnProps extends Omit<React.ComponentProps<'input'>, 'size'> {
  label: string;
  name: string;
  tabIndex?: number;
  errorMessage?: string;
  placeholder?: string;
  value?: string;
  className?: string;
  defaultValue?: string;
  errored?: boolean;
  disabled?: boolean;
  validated?: boolean;
  id?: string;
  helper?: string | React.ReactNode;
  type?: 'text' | 'number' | 'tel' | 'email' | 'password' | 'search';
  onBlur?: React.FocusEventHandler<HTMLInputElement>;
  onFocus?: React.FocusEventHandler<HTMLInputElement>;
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  wrapperProps?: InputWrapperProps;
  PostComponent?: React.ReactNode;
  LeadingIcon?: React.ComponentType<IconProps>;
  dataMarshalComponent?: string;
  size?: InputSizes;
  variant?: InputVariants;
}

export type InputProps = PropsWithRef<InputOwnProps>;

export const Input = forwardRef(
  (props: InputProps, ref: React.RefObject<HTMLInputElement>) => {
    const {
      defaultValue,
      errored = false,
      errorMessage,
      placeholder,
      className,
      label,
      name,
      value,
      type = 'text',
      helper,
      disabled = false,
      validated = false,
      onBlur,
      onFocus,
      onChange,
      wrapperProps,
      PostComponent = null,
      size = 'md',
      LeadingIcon,
      dataMarshalComponent = 'Input',
      variant,
    } = props;
    const id = props.id || name;
    const inputRef = useRef<HTMLInputElement>(null);
    const [isFocused, setIsFocused] = useState(false);
    const [hasRefValue, setHasRefValue] = useState(false);
    const [isFirstRender, setIsFirstRender] = useState(true);
    const [controlledValue, setControlledValue] = useState(value);

    const combinedRefs = mergeRefs([inputRef, ref]);

    const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
      setHasRefValue(!!inputRef?.current?.value);
      setIsFocused(false);
      if (onBlur) {
        onBlur(e);
      }
    };

    const handleChange = React.useCallback(
      (e: React.ChangeEvent<HTMLInputElement>) => {
        if (typeof value !== 'undefined') setControlledValue(e.target.value);
        if (onChange) {
          onChange(e);
        }
      },
      [onChange, value],
    );

    useEffect(() => {
      setHasRefValue(!!inputRef?.current?.value);
      setIsFirstRender(false);
      if (typeof value !== 'undefined') setControlledValue(value);
    }, [value]);

    const handleFocus = (e: React.FocusEvent<HTMLInputElement>) => {
      setIsFocused(true);
      if (onFocus) {
        onFocus(e);
      }
    };

    const otherProps: React.ComponentProps<'input'> = omit(props, [
      'errored',
      'validated',
      'PostComponent',
      'helper',
      'label',
      'errorMessage',
      'wrapperProps',
      'LeadingIcon',
      'dataMarshalComponent',
      'size',
      'variant',
    ]);

    const isActive =
      (isFirstRender && defaultValue) ||
      hasRefValue ||
      value ||
      placeholder ||
      isFocused;

    const shouldShowLabel = !(size === 'sm' && isActive);

    return (
      <div
        className={classnames(
          'formField',
          'formField-input',
          `formField-${size}`,
          className,
          {
            [`formField-${variant!}`]: variant,
            'is-validated': validated,
            'is-disabled': disabled,
            'is-errored': errored,
            'is-active': isActive,
            'is-focused': isFocused,
            'has-leadingIcon': !!LeadingIcon,
          },
        )}
        data-marshalcomponent={dataMarshalComponent}
      >
        <div
          {...wrapperProps}
          className={classnames(
            'formField_control',
            'formField_wrapper',
            wrapperProps?.className,
          )}
        >
          {LeadingIcon && (
            <LeadingIcon className="formField_icon formField_icon-leading" />
          )}
          <input
            {...otherProps}
            className="formField_input"
            id={id}
            onBlur={handleBlur}
            onChange={handleChange}
            onFocus={handleFocus}
            ref={combinedRefs}
            type={type}
            value={controlledValue}
          />
          {shouldShowLabel && (
            <label className="formField_placeholder" htmlFor={id}>
              {label}
            </label>
          )}
          {errored && (
            <IcOctagonCross className="formField_icon formField_icon-error" />
          )}
          {disabled && (
            <IcLock className="formField_icon formField_icon-disabled" />
          )}
          {validated && (
            <IcShieldCheck className="formField_icon formField_icon-validated" />
          )}
          {PostComponent}
        </div>
        {errored && errorMessage && (
          <label className="formField_error" htmlFor={id}>
            {errorMessage}
          </label>
        )}
        {helper && <p className="formField_helper">{helper}</p>}
      </div>
    );
  },
);
