import { IcSuccess } from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { forwardRef, PropsWithRef, useRef } from 'react';
import mergeRefs from 'react-merge-refs';

export interface CheckboxOwnProps
  extends Omit<React.ComponentProps<'input'>, 'children'> {
  errored?: boolean;
  label: string;
  description?: string;
  id: string;
  labelClassName?: string;
}

export type CheckboxProps = PropsWithRef<CheckboxOwnProps>;

export const Checkbox = forwardRef(
  (props: CheckboxProps, ref: React.RefObject<HTMLInputElement>) => {
    const {
      className,
      labelClassName,
      label,
      errored,
      description,
      disabled,
      id,
      ...restProps
    } = props;

    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    return (
      <div
        className={classnames('formField', 'formField-checkbox', className, {
          'is-errored': errored,
          'is-disabled': disabled,
        })}
        data-marshalcomponent="Checkbox"
      >
        <div className="formField_control">
          <input
            {...restProps}
            className="formField_checkbox"
            disabled={disabled}
            id={id}
            ref={combinedRefs}
            type="checkbox"
          />
          <label
            className={classnames('formField_label', labelClassName)}
            htmlFor={id}
          >
            <IcSuccess className="formField_symbol" />
            {label}
            {description && (
              <p className="formField_description">{description}</p>
            )}
          </label>
        </div>
      </div>
    );
  },
);
