import sortedBy from 'lodash/sortBy';

import { StatusDataItem } from './StatusData';
import { StatusItem } from './StatusItem';

const sortListByName = (list: StatusDataItem[]) => sortedBy(list, 'name');

interface StatusListProps {
  statusDataList: StatusDataItem[];
}

const StatusList = ({ statusDataList }: StatusListProps) => {
  return (
    <>
      {sortListByName(statusDataList).map((statusDataItem) => (
        <>
          <StatusItem
            key={statusDataItem.name}
            statusDataItem={statusDataItem}
          />
          {statusDataItem.components &&
            sortListByName(statusDataItem.components).map((component) => (
              <StatusItem
                key={component.name}
                statusDataItem={component}
                tabbed
              />
            ))}
        </>
      ))}
    </>
  );
};

export { StatusList };
