import classnames from 'classnames';
import type { ElementType, ReactNode } from 'react';
import type { PolymorphicPropsWithoutRef } from 'react-polymorphic-types';

const defaultElement = 'nav';

export interface SidebarOwnOwnProps {
  SidebarTopComponent?: ReactNode;
  SidebarBottomComponent?: ReactNode;
}

export type SidebarProps<Element extends ElementType = typeof defaultElement> =
  PolymorphicPropsWithoutRef<SidebarOwnOwnProps, Element>;

export function Sidebar<Element extends ElementType = typeof defaultElement>({
  as,
  children,
  className,
  SidebarTopComponent = null,
  SidebarBottomComponent = null,
  ...restProps
}: SidebarProps<Element>) {
  const RenderElement: ElementType = as || defaultElement;

  return (
    <RenderElement
      className={classnames('layout_menu', 'sidebar', className)}
      {...restProps}
    >
      <div className="sidebar_top">{SidebarTopComponent}</div>
      <div className="sidebar_content">{children}</div>
      <div className="sidebar_bottom">{SidebarBottomComponent}</div>
    </RenderElement>
  );
}
