import { Button } from '../../Button';

interface MultiSelectActionsProps {
  onApply: () => void;
  onCancel: () => void;
  applyLabel: string;
  cancelLabel: string;
}

export const MultiSelectActions = ({
  onApply,
  onCancel,
  applyLabel,
  cancelLabel,
}: MultiSelectActionsProps) => {
  return (
    <div className="multiSelectActions">
      <Button
        className="multiSelectActions_button"
        cssType="tertiary"
        onClick={onCancel}
        size="sm"
        type="button"
      >
        {cancelLabel}
      </Button>
      <Button
        className="multiSelectActions_button"
        cssType="secondary"
        onClick={() => onApply()}
        size="sm"
        type="button"
      >
        {applyLabel}
      </Button>
    </div>
  );
};
