import classnames from 'classnames';
import { PropsWithChildren, ReactElement, useEffect, useState } from 'react';
import { createPortal } from 'react-dom';

export type ModalProps = PropsWithChildren<{
  attachTo?: HTMLElement;
  className?: string;
  closeIcon?: ReactElement | false;
  dataTestid?: string;
  onCloseModal?: () => void;
  animationsDisabled?: boolean;
  position?: 'top' | 'right' | 'bottom' | 'left' | 'center';
}>;

const Modal = ({
  attachTo = document.getElementById('app') || document.body,
  position = 'center',
  children,
  onCloseModal,
  closeIcon,
  className,
  dataTestid = 'modal',
  animationsDisabled = false,
}: ModalProps) => {
  const [shouldRenderModal, setShouldRenderModal] =
    useState(animationsDisabled);

  useEffect(() => {
    // Open the dialog when the component is mounted
    if (!animationsDisabled) {
      setTimeout(() => {
        setShouldRenderModal(true);
      }, 100);
    }
  }, [animationsDisabled]);

  const handleModalClose = () => {
    if (animationsDisabled) {
      onCloseModal?.();
    } else {
      setShouldRenderModal(false);
    }
  };

  const onTransitionEnd = () => {
    if (!shouldRenderModal) {
      if (onCloseModal) onCloseModal();
    }
  };

  const modalWrapperClassNames = classnames([
    'modal',
    className,
    `modal-${position}`,
    shouldRenderModal && 'modal-open',
    animationsDisabled && 'modal-animationDisabled',
  ]);

  const modalContent = (
    <div
      className={modalWrapperClassNames}
      data-marshalcomponent="Modal"
      data-testid={dataTestid}
    >
      <div
        className="modal_backdrop"
        data-testid="modalBackdrop"
        onClick={handleModalClose}
      />
      <div
        className="modal_content"
        data-cy={`modalContent-${position}`}
        data-testid="modalContent"
        onTransitionEnd={onTransitionEnd}
      >
        {closeIcon && (
          <section className="modal_closeIconContainer">
            <span data-testid="modalClose" onClick={handleModalClose}>
              {closeIcon}
            </span>
          </section>
        )}
        {children}
      </div>
    </div>
  );

  return attachTo ? createPortal(modalContent, attachTo) : modalContent;
};

export { Modal };
