import IconsArray from '../Icons/iconsArray';
import { AssetsWrapper } from '../lib/AssetsWrapper';

/* eslint-disable camelcase */
export const LogosWrapper = () => {
  const { logos_badges_store, logos_cabify, logos_carbon_neutral } =
    IconsArray[0];
  const LogosArrayObject = {
    logos_badges_store,
    logos_cabify,
    logos_carbon_neutral,
  };
  return AssetsWrapper(LogosArrayObject, 'sb-icons_list-logos');
};
