import classnames from 'classnames';
import { forwardRef, PropsWithRef, RefObject, useMemo } from 'react';

import { Password, PasswordProps } from '../Password';

export interface PasswordEntropyOwnProps extends PasswordProps {
  score?: number;
}

export type PasswordEntropyProps = PropsWithRef<PasswordEntropyOwnProps>;

export const PasswordEntropy = forwardRef(
  (
    { score, ...otherProps }: PasswordEntropyOwnProps,
    ref: RefObject<HTMLInputElement>,
  ) => {
    const entropyClassName = useMemo(
      () =>
        classnames(
          'formField_icon',
          'formField_entropy',
          'passwordEntropyMeter',
          {
            'passwordEntropyMeter-weak': score === 0,
            'passwordEntropyMeter-notGreat': score === 1,
            'passwordEntropyMeter-notBad': score === 2,
            'passwordEntropyMeter-good': score === 3 || score === 4,
            'passwordEntropyMeter-invalid': score === -1,
          },
        ),
      [score],
    );

    const EntropyComponent = () => {
      return (
        <span
          className={entropyClassName}
          data-testid={`entropyMeter-${otherProps.name}`}
        />
      );
    };

    return (
      <Password
        {...otherProps}
        className={classnames(
          otherProps.className,
          'formField-passwordEntropy',
        )}
        dataMarshalComponent="PasswordEntropy"
        EntropyComponent={<EntropyComponent />}
        ref={ref}
      />
    );
  },
);
