import classnames from 'classnames';
import type { ElementType } from 'react';
import type { PolymorphicPropsWithoutRef } from 'react-polymorphic-types';

const defaultElement = 'ul';

// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface SidebarListOwnProps {}

export type SidebarListProps<
  Element extends ElementType = typeof defaultElement,
> = PolymorphicPropsWithoutRef<SidebarListOwnProps, Element>;

export function SidebarList<
  Element extends ElementType = typeof defaultElement,
>({ as, className, ...restProps }: SidebarListProps<Element>) {
  const RenderElement: ElementType = as || defaultElement;

  return (
    <RenderElement
      className={classnames('sidebar_list', className)}
      {...restProps}
    />
  );
}
