import React, { useCallback, useEffect, useRef, useState } from 'react';

function useComponentVisible(initialIsVisible: boolean) {
  const [isComponentVisible, setIsComponentVisible] =
    useState(initialIsVisible);
  const visibleRef: React.MutableRefObject<HTMLInputElement | null> =
    useRef(null);

  const handleHideDropdown = (e: KeyboardEvent) => {
    if (e.key === 'Escape') {
      setIsComponentVisible(false);
    }
  };

  const handleClickOutside = useCallback(
    (e) => {
      if (
        visibleRef.current &&
        !visibleRef.current.contains(e.target as Node)
      ) {
        setIsComponentVisible(false);
      }
    },
    [visibleRef],
  );

  useEffect(() => {
    document.addEventListener('keydown', handleHideDropdown, true);
    document.addEventListener('click', handleClickOutside, true);
    return () => {
      document.removeEventListener('keydown', handleHideDropdown, true);
      document.removeEventListener('click', handleClickOutside, true);
    };
  });

  return { visibleRef, isComponentVisible, setIsComponentVisible };
}

export default useComponentVisible;
