import {
  Tabs as BaseTabs,
  TabsOrientation,
  TabsProps as BaseTabsProps,
} from '@reach/tabs';
import type { ForwardRefComponent } from '@reach/utils/polymorphic';
import classnames from 'classnames';
import { ComponentProps, forwardRef } from 'react';

import { Tab } from './Tab';
import { TabList } from './TabList';
import { TabPanel } from './TabPanel';
import { TabPanels } from './TabPanels';

export type TabSizes = 'md' | 'lg';

interface OwnTabsProps extends Omit<BaseTabsProps, 'orientation'> {
  size?: TabSizes;
}

export type ChildrenTabsProps = ComponentProps<
  ForwardRefComponent<'div', OwnTabsProps>
>;

export type TabsProps = ChildrenTabsProps;

const TabsComponent: ForwardRefComponent<'div', TabsProps> = forwardRef(
  ({ size = 'md', className, ...props }, ref) => (
    <BaseTabs
      {...props}
      className={classnames('tabs', `tabs-${size}`, className)}
      orientation={TabsOrientation.Horizontal}
      ref={ref}
    >
      {props.children}
    </BaseTabs>
  ),
);

const Tabs = Object.assign(TabsComponent, {
  TabList,
  Tab,
  Panels: TabPanels,
  Panel: TabPanel,
});

export { Tabs };
