import classnames from 'classnames';
import React, { useEffect, useState } from 'react';

import { Input } from '../Input';
import useComponentVisible from './hooks/useComponentVisible';
import useRoveFocus from './hooks/useRoveFocus';
import { OptionProps } from './Option';
import { SelectInput } from './SelectInput';

type OptionChildren = React.ReactElement<OptionProps>;

export interface SelectProps
  extends Omit<
    React.ComponentProps<typeof Input>,
    'onChange' | 'onClick' | 'children'
  > {
  children: OptionChildren | OptionChildren[] | undefined;
  errored?: boolean;
  onChange?: (val: string) => void;
  onClick?: () => void | React.MouseEventHandler<HTMLInputElement>;
  expanded?: boolean;
  helper?: string | React.ReactNode;
  errorMessage?: string;
}

export const Select = ({
  value,
  children,
  className,
  onChange,
  errored,
  expanded = false,
  helper,
  errorMessage,
  ...restProps
}: SelectProps) => {
  const listItems = React.useMemo(
    () => React.Children.toArray(children),
    [children],
  ) as OptionChildren[];

  const getContentFromValue = (val?: string) =>
    listItems.find((c) => c.props.value === val)?.props.children;

  const [focus, setFocus] = useRoveFocus(listItems.length);
  const { visibleRef, isComponentVisible, setIsComponentVisible } =
    useComponentVisible(expanded);

  const selectedValue = React.useMemo(
    () => listItems.find((c) => c.props.selected),
    [listItems],
  );

  const isValueValid = value && listItems.some((c) => c.props.value === value);

  const initialValue =
    (isValueValid ? value : undefined) ||
    (selectedValue ? selectedValue.props.value : undefined) ||
    listItems[0]?.props.value;

  const [controlledValue, setControlledValue] = useState(initialValue);

  const handleClick = React.useCallback(
    (val: string) => {
      setIsComponentVisible(false);
      setControlledValue(val);
      if (onChange) {
        onChange(val);
      }
    },
    [onChange, setIsComponentVisible],
  );

  useEffect(() => {
    if ((value || value === '') && isValueValid) {
      setControlledValue(value);
    }
  }, [value, isValueValid]);

  const Options = React.useMemo(
    () => () => {
      return (
        <div className="contextualPopup">
          <div className="select_wrapper contextualPopup-content">
            <ul className="select_options">
              {listItems.map((comp, index) =>
                React.cloneElement(comp, {
                  onClick: handleClick,
                  key: comp.props.value,
                  index,
                  setFocus: setFocus as (value: number) => void,
                  initialValue: controlledValue,
                  focus: focus === index,
                  initialRef: visibleRef,
                }),
              )}
            </ul>
          </div>
        </div>
      );
    },
    [controlledValue, focus, handleClick, listItems, setFocus, visibleRef],
  );

  return (
    <SelectInput
      {...restProps}
      className={classnames('formField-select', className, {
        'is-focused': isComponentVisible,
      })}
      content={getContentFromValue(controlledValue)}
      errored={errored}
      errorMessage={errorMessage}
      helper={helper}
      isDropdownVisible={isComponentVisible}
      onClick={() => setIsComponentVisible(!isComponentVisible)}
      value={controlledValue}
      visibleRef={visibleRef}
    >
      {isComponentVisible ? <Options /> : null}
    </SelectInput>
  );
};
