export const buildAvatarClassName = (id: string) => {
  let numberFromId = Number(id.replace(/\D/g, '').slice(-5));
  numberFromId %= 3;
  return `avatar-${numberFromId}`;
};

export const buildAvatarInitials = (name: string, surname: string) => {
  let initials = '';
  if (name) initials += name[0];
  if (surname) initials += surname[0];
  return initials;
};
