import React, { useCallback, useState } from 'react';

export const useClick = (onClick?: (event: React.MouseEvent) => void) => {
  const [clicked, setClick] = useState(false);
  return {
    clicked,
    onClick: useCallback(
      (
        event: React.MouseEvent<
          HTMLButtonElement | HTMLAnchorElement,
          MouseEvent
        >,
      ) => {
        (event.target as HTMLButtonElement | HTMLAnchorElement).blur();
        setClick(true);
        if (onClick) {
          onClick(event);
        }
      },
      [onClick],
    ),
  };
};
