import { IcLocationAlternate, Icons } from '@product/marshal-tokens';
import React, { forwardRef, useRef } from 'react';
import mergeRefs from 'react-merge-refs';

import { Option, Select, SelectProps } from '../Select';

interface SelectCountryItem {
  code: string;
  prefix: string;
  name: string;
  selected?: boolean;
}

export interface SelectCountryProps extends Omit<SelectProps, 'children'> {
  countryCodes: SelectCountryItem[];
}

export const SelectCountry = forwardRef(
  (
    { countryCodes, ...props }: SelectCountryProps,
    ref: React.RefObject<HTMLInputElement>,
  ) => {
    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    return (
      <Select
        {...props}
        className="formField-selectCountry"
        dataMarshalComponent="SelectCountry"
        ref={combinedRefs}
      >
        {countryCodes.map((countryCode) => {
          const flagCode = `${countryCode.code.charAt(0)}${countryCode.code
            .slice(1)
            .toLowerCase()}`;
          const Icon = Icons[`IcFlag${flagCode}`] || IcLocationAlternate;
          return (
            <Option
              key={countryCode.code}
              selected={
                props.value
                  ? props.value === countryCode.code
                  : countryCode.selected
              }
              value={countryCode.code}
            >
              <Icon className="select_icon" />
              <span className="select_value">{countryCode.name}</span>
            </Option>
          );
        })}
      </Select>
    );
  },
);
