import {
  ChangeEvent,
  cloneElement,
  forwardRef,
  ReactElement,
  ReactNode,
  RefObject,
  useRef,
  useState,
} from 'react';
import mergeRefs from 'react-merge-refs';

import { SelectionControlsGroup } from '../../SelectionControlsGroup';

export interface RadioGroupProps {
  className?: string;
  children: ReactElement[];
  value?: string;
  name: string;
  errored?: boolean;
  errorMessage?: string;
  helper?: string | ReactNode;
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
}

export const RadioGroup = forwardRef(
  (props: RadioGroupProps, ref: RefObject<HTMLInputElement>) => {
    const {
      children,
      className,
      onChange,
      value,
      errored,
      errorMessage,
      helper,
      name,
    } = props;
    const checkedValue = children.find((c) => c.props.checked);
    const initialValue =
      value || (checkedValue ? checkedValue.props.value : undefined);
    const [inputChecked, setInputChecked] = useState(initialValue);

    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
      setInputChecked(e.target.value);
      if (onChange) {
        onChange(e);
      }
    };

    return (
      <SelectionControlsGroup
        className={className}
        errored={errored}
        errorMessage={errorMessage}
        helper={helper}
        onChange={handleChange}
      >
        {children.map((comp: ReactElement) => {
          return cloneElement(comp, {
            checked: inputChecked === comp.props.value,
            key: comp.props.id,
            name,
            ref: combinedRefs,
          });
        })}
      </SelectionControlsGroup>
    );
  },
);

RadioGroup.defaultProps = {};
