import classnames from 'classnames';
import type { PropsWithChildren, ReactNode } from 'react';

import { Sidebar } from './Sidebar/Sidebar';
import { SidebarAvatar } from './Sidebar/SidebarAvatar';
import { SidebarList } from './Sidebar/SidebarList';
import { SidebarListItem } from './Sidebar/SidebarListItem';

export type LayoutProps = PropsWithChildren<{
  toggleSidebar?: VoidFunction;
  // closeSidebar?: VoidFunction;
  pageClassName?: string;
  sidebarClassName?: string;
  sectionClassName?: string;
  mainClassName?: string;
  pageTitle?: string;
  SidebarComponent?: ReactNode;
  MainTopComponent?: ReactNode;
  MainBottomComponent?: ReactNode;
  showSidebar?: boolean;
}>;

export const Layout = ({
  toggleSidebar,
  showSidebar = false,
  children,
  pageClassName: pageClassNameProp = 'layout_wrap',
  sectionClassName,
  mainClassName = 'layout_content',
  SidebarComponent = null,
  MainTopComponent = null,
  MainBottomComponent = null,
  pageTitle,
}: LayoutProps) => {
  const pageClassName = classnames(
    pageClassNameProp,
    sectionClassName && `section-${sectionClassName}`,
  );

  return (
    <article className="maincontent">
      <div
        className={classnames({
          menuinteraction: showSidebar,
        })}
      >
        <div className={pageClassName}>
          {SidebarComponent}
          <div className={mainClassName}>
            <nav className="sidebar_navbar" onClick={toggleSidebar}>
              <div className="sidebar_hamburger">
                <span />
              </div>
              {pageTitle}
            </nav>
            {MainTopComponent}
            <div className="layout_page">{children}</div>
            {MainBottomComponent}
          </div>
        </div>
      </div>
    </article>
  );
};

Layout.Sidebar = Sidebar;
Layout.SidebarList = SidebarList;
Layout.SidebarListItem = SidebarListItem;
Layout.SidebarAvatar = SidebarAvatar;
