import omit from 'lodash/omit';
import React, {
  ChangeEventHandler,
  forwardRef,
  InputHTMLAttributes,
  useEffect,
  useRef,
  useState,
} from 'react';
import mergeRefs from 'react-merge-refs';

export interface RangeInputProps
  extends Omit<InputHTMLAttributes<HTMLInputElement>, 'onChange' | 'value'> {
  onChange?: (value: number) => void;
  value?: number;
  min?: number;
  max?: number;
  label?: string;
}

const RangeInput = forwardRef(
  (
    { onChange, value = 0, min = 0, max = 100, ...inputProps }: RangeInputProps,
    ref: React.RefObject<HTMLInputElement>,
  ) => {
    const [currentValue, setCurrentValue] = useState(value);
    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);

    const otherInputProps = omit(inputProps, ['label']);

    useEffect(() => {
      setCurrentValue(value);
    }, [value]);

    const onInputChange: ChangeEventHandler<HTMLInputElement> = (e) => {
      const newValue = parseInt(e.target.value, 10);
      setCurrentValue(newValue);
      if (onChange) {
        onChange(newValue);
      }
    };

    const progressStyle = () => {
      const range = max - min;
      const ratio = (currentValue - min) / range;

      return {
        left: 0,
        width: `${ratio * 100}%`,
      };
    };

    return (
      <div
        className="formField formField-slider"
        data-marshalcomponent="RangeInput"
      >
        {inputProps.label && (
          <div className="formField_label">{inputProps.label}</div>
        )}
        <div className="formField_sliderBox">
          <span className="formField_progressLine" style={progressStyle()} />
          <input
            {...otherInputProps}
            className="formField_sliderInput"
            max={max}
            min={min}
            onChange={onInputChange}
            ref={combinedRefs}
            type="range"
            value={currentValue}
          />
        </div>
      </div>
    );
  },
);

export { RangeInput };
