export type StepStatus = 'done' | 'previous' | 'current' | 'next';

export const inferStepStatus = ({
  currentStepIndex,
  stepIndex,
}: {
  currentStepIndex: number;
  stepIndex: number;
}): StepStatus => {
  if (stepIndex === currentStepIndex) return 'current';
  if (stepIndex > currentStepIndex) return 'next';
  if (stepIndex === currentStepIndex - 1) return 'previous';
  return 'done';
};
