import Fuse from 'fuse.js';

import { SearchProps } from '../Search/Search';
import { MultiSelectSearchBarBase } from './MultiSelectSearchBarBase';

export interface SearchListItem {
  key: string;
  value: string;
}

interface MultiSelectSearchBarProps extends SearchProps {
  items: SearchListItem[];
  onSearch: (result: SearchListItem[]) => void;
}

const SEARCH_OPTIONS = {
  keys: ['value'],
  includeScore: false,
  threshold: 0.4,
  minChars: 3,
};

export const MultiSelectSearchBar = ({
  items,
  onSearch,
  ...restProps
}: MultiSelectSearchBarProps) => {
  const handleSearchChange = (value: string) => {
    const searchTerms = value.trim();
    const areTermsValid = searchTerms?.length >= 3;

    const searchResult = areTermsValid
      ? new Fuse(items, SEARCH_OPTIONS).search(searchTerms).map((r) => r.item)
      : items;

    onSearch(searchResult);
  };

  return (
    <div className="searchBar">
      <MultiSelectSearchBarBase
        minChars={SEARCH_OPTIONS.minChars}
        onChange={handleSearchChange}
        {...restProps}
      />
    </div>
  );
};
