import {
  IcCircleCheck,
  IcCircleCross,
  IcCircleInfo,
  IcTriangleExclamation,
  IcVoucher,
} from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { forwardRef, PropsWithRef, useRef } from 'react';
import mergeRefs from 'react-merge-refs';

const ICON_COMPONENT = {
  featured: IcCircleInfo,
  positive: IcCircleCheck,
  negative: IcCircleCross,
  warning: IcTriangleExclamation,
  promotion: IcVoucher,
} as const;

export interface BannerOwnProps {
  icon?: React.ElementType;
  className?: string;
  variant?: 'featured' | 'negative' | 'warning' | 'positive' | 'promotion';
  /**
   * @name title
   * @description Use this prop to display a title over the description. This element will only
   * allow inline elements.
   */
  title?: React.ReactNode;
  /**
   * @name inlineAction
   * @description Use this prop to place link or buttons after the description block.
   */
  inlineAction?: React.ReactNode;
  /**
   * @name trailingAction
   * @description Use this prop to place actions on the very end of the banner, placed on description
   * right.
   */
  trailingAction?: React.ReactNode;
  /**
   * @name blockActions
   * @description Use this prop to compose actions placed below description block.
   */
  blockActions?: React.ReactNode;
  /**
   * @name children
   * @description Children prop should only contain inline elements as this component children
   * container is optimized to only display inline elements correctly.
   */
  children: React.ReactNode;
  HtmlTagName?: 'div' | 'li';
}

export type BannerProps = PropsWithRef<BannerOwnProps>;

const Banner = forwardRef(
  (
    {
      icon,
      variant = 'featured',
      inlineAction,
      trailingAction,
      className,
      children,
      title,
      blockActions,
      HtmlTagName = 'div',
    }: BannerProps,
    ref: React.RefObject<HTMLLIElement>,
  ) => {
    const bannerRef = useRef<HTMLDivElement | HTMLLIElement>(null);
    const combinedRefs = mergeRefs([bannerRef, ref]);

    const Icon = icon || ICON_COMPONENT[variant];

    return (
      <HtmlTagName
        className={classnames(
          `notification banner banner-${variant}`,
          className,
        )}
        data-marshalcomponent="Banner"
        ref={combinedRefs}
      >
        <Icon className="banner_icon" role="img" />
        <div className="notification_description banner_description">
          {title && <p className="banner_title">{title}</p>}
          <span className="banner_message">{children}</span>
          {inlineAction && (
            <span className="banner_actionButton">{inlineAction}</span>
          )}
          {blockActions && (
            <div className="banner_actionButton banner_blockActions">
              {blockActions}
            </div>
          )}
        </div>
        {trailingAction && (
          <div className="notification_actionIcon">{trailingAction}</div>
        )}
      </HtmlTagName>
    );
  },
);

export { Banner };
