import { Avatar, AvatarProps } from '../../Avatar';

interface SidebarAvatarProps extends AvatarProps {
  title?: string;
  subtitle?: string;
}

export function SidebarAvatar({
  title,
  subtitle,
  ...props
}: SidebarAvatarProps) {
  return (
    <div className="sidebar_avatar">
      <Avatar size="lg" {...props} />
      <p className="sidebar_avatarName">{`${props.name} ${props.surname}`}</p>
      {title && <p className="sidebar_avatarTitle">{title}</p>}
      {subtitle && <p className="sidebar_avatarSubtitle">{subtitle}</p>}
    </div>
  );
}
