import { noop } from '@product/utils-drawer';
import classnames from 'classnames';
import { HTMLProps, MouseEvent, useMemo } from 'react';

import { getHtmlProps } from '../utils/getHtmlProps';

type GridAlignment = 'center' | 'right' | 'around' | 'between';

export interface GridProps extends HTMLProps<HTMLDivElement> {
  alignment: GridAlignment | null;
  as: string;
  className: string;
  gap?: boolean;
}

const Grid = (props: GridProps) => {
  const handleClick = (e: MouseEvent<HTMLDivElement>) => {
    if (typeof props.onClick === 'function') {
      props.onClick(e);
    }
  };

  const renderClass = useMemo(
    () =>
      classnames({
        [`grid ${props.className}`]: true,
        [`is-gapless`]: !props.gap,
        [`justify-content-${props.alignment!}`]: props.alignment,
      }),
    [props.className, props.gap, props.alignment],
  );

  const HtmlTagName = props.as.toString() as 'div';
  return (
    <HtmlTagName
      className={renderClass}
      data-marshalcomponent="Grid"
      onClick={handleClick}
      {...getHtmlProps(props, 'alignment', 'className', 'gap', 'onClick', 'as')}
    >
      {props.children}
    </HtmlTagName>
  );
};

Grid.defaultProps = {
  as: 'div',
  alignment: null,
  className: '',
  gap: true,
  onClick: noop,
};

export { Grid };
