import classnames from 'classnames';
import type { ElementType } from 'react';

import type { ButtonProps } from '../components/Button';
import { useClick } from './useClick';

export const useLoadableButton = <ET extends ElementType>(
  props: ButtonProps<ET>,
) => {
  const {
    className,
    size,
    display,
    cssType,
    submitting,
    submittingOnClick,
    disabled,
  } = props;
  const { clicked, onClick } = useClick(props.onClick);
  return {
    onClick,
    className: classnames('button', className, {
      [`button-${size!}`]: size,
      [`button-${display!}`]: display,
      [`button-${cssType!}`]: cssType && cssType !== 'none',
      'is-submitting': submitting || (submittingOnClick && clicked),
      'is-disabled': disabled,
    }),
  };
};
