import {
  IcArrowRightMd,
  IcDestinationMini,
  IconProps,
  IcSuccess,
} from '@product/marshal-tokens';
import classNames from 'classnames';
import { HTMLProps } from 'react';

import { StepStatus } from './utils/inferStepStatus';

const ICONS_MAP: Record<StepStatus, React.ComponentType<IconProps>> = {
  previous: IcSuccess,
  current: IcArrowRightMd,
  done: IcSuccess,
  next: IcDestinationMini,
};

export interface StepIndicatorIconProps extends HTMLProps<SVGElement> {
  stepStatus: StepStatus;
}

export const StepIndicatorIcon = ({ stepStatus }: StepIndicatorIconProps) => {
  const Icon = ICONS_MAP[stepStatus];

  return (
    <Icon
      aria-label={stepStatus ? `${stepStatus} step` : 'step'}
      className={classNames(
        'stepIndicator_icon',
        `stepIndicator_icon-${stepStatus}`,
      )}
    />
  );
};
