import classnames from 'classnames';
import type { ElementType } from 'react';
import type { PolymorphicPropsWithoutRef } from 'react-polymorphic-types';

const defaultElement = 'a';

export type SidebarListItemSize = 'sm' | 'md';
export interface SidebarListItemOwnProps {
  active?: boolean;
  size?: SidebarListItemSize;
}

export type SidebarListItemProps<
  Element extends ElementType = typeof defaultElement,
> = PolymorphicPropsWithoutRef<SidebarListItemOwnProps, Element>;

export function SidebarListItem<
  Element extends ElementType = typeof defaultElement,
>({
  as,
  active,
  size = 'md',
  className,
  ...restProps
}: SidebarListItemProps<Element>) {
  const RenderElement: ElementType = as || defaultElement;
  return (
    <li
      className={classnames('sidebar_listItem', className, {
        'is-active': active,
        [`sidebar_listItem-${size}`]: size,
      })}
    >
      <RenderElement {...restProps} />
    </li>
  );
}
