import { useEffect, useMemo, useState } from 'react';

import { Checkbox } from '..';
import useRoveFocus from '../Select/hooks/useRoveFocus';
import { Option } from '../Select/Option';
import { MultiSelectLiterals, MultiSelectOption } from './MultiSelect';
import { MultiSelectActions } from './MultiSelectActions';
import { MultiSelectSearchBar } from './MultiSelectSearchBar';
import { MultiSelectSearchBarBase } from './MultiSelectSearchBarBase';

const SELECT_ALL_KEY = 'checkbox-selectAll';

interface MultiSelectDropdownProps {
  onApply: (selectedKeys: string[]) => void;
  onCancel: () => void;
  onCustomSearch?: (searchTerms: string) => void;
  isSelectActive: boolean;
  options: MultiSelectOption[];
  selectedKeys: string[];
  literals: MultiSelectLiterals;
  visibleRef: React.MutableRefObject<HTMLInputElement | null>;
}

export const MultiSelectDropdown = ({
  onApply,
  onCancel,
  onCustomSearch,
  isSelectActive,
  options,
  selectedKeys,
  literals,
  visibleRef,
}: MultiSelectDropdownProps) => {
  const {
    applyLabel,
    cancelLabel,
    emptyOptionsText,
    selectAllLabel,
    searchInputLabel,
  } = literals;

  const [filteredOptions, setFilteredOptions] = useState(options);
  const [focus, setFocus] = useRoveFocus(filteredOptions.length);

  const [temporarilySelectedKeys, setTemporarylySelectedKeys] = useState(
    new Set(selectedKeys),
  );

  useEffect(() => {
    setTemporarylySelectedKeys(new Set(selectedKeys));
  }, [isSelectActive, selectedKeys]);

  useEffect(() => {
    setFilteredOptions(options);
  }, [isSelectActive, options]);

  const optionKeys = useMemo(() => options.map((op) => op.key), [options]);

  const handleCheckboxChange = (optionKey: string, checked: boolean) => {
    setTemporarylySelectedKeys((previousTempOptions) => {
      checked
        ? previousTempOptions.add(optionKey)
        : previousTempOptions.delete(optionKey);

      return new Set(previousTempOptions);
    });
  };

  const handleSelectAllChanged = (checked: boolean) => {
    const newKeys = checked ? optionKeys : [];

    setTemporarylySelectedKeys(new Set<string>(newKeys));
  };

  // @TODO:  add keyboard navigation support
  const { length: optionsLength } = options;
  const dropdownOptions = useMemo(() => {
    const isAllSelected = temporarilySelectedKeys.size === optionsLength;

    return (
      <>
        <Option
          focus={focus === 0}
          index={0}
          initialRef={visibleRef}
          key={SELECT_ALL_KEY}
          onClick={() => null}
          setFocus={setFocus as (value: number) => void}
          value={SELECT_ALL_KEY}
        >
          <Checkbox
            checked={isAllSelected}
            id={SELECT_ALL_KEY}
            label={selectAllLabel}
            onChange={(event) => handleSelectAllChanged(event.target.checked)}
          />
        </Option>

        <hr className="select_separator" />

        {filteredOptions.map((option, index) => (
          <Option
            focus={focus === index + 1}
            index={index + 1}
            initialRef={visibleRef}
            key={option.key}
            onClick={() => null}
            setFocus={setFocus as (value: number) => void}
            value={option.key}
          >
            <Checkbox
              checked={temporarilySelectedKeys.has(option.key)}
              id={`checkbox-${option.key}`}
              key={`checkbox-${option.key}`}
              label={option.value}
              onChange={(event) =>
                handleCheckboxChange(option.key, event.target.checked)
              }
            />
          </Option>
        ))}
      </>
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filteredOptions, optionsLength, selectAllLabel, temporarilySelectedKeys]);

  const dropdownEmpty = <li className="select_option">{emptyOptionsText}</li>;

  return (
    <div className="contextualPopup" data-testid="MultiSelectDropdown">
      <div className="contextualPopup_options">
        {onCustomSearch ? (
          <MultiSelectSearchBarBase
            label={searchInputLabel}
            name="searchInput"
            onChange={onCustomSearch}
          />
        ) : (
          <MultiSelectSearchBar
            items={options}
            label={searchInputLabel}
            name="searchInput"
            onSearch={setFilteredOptions}
          />
        )}
        <div className="select_wrapper contextualPopup-content">
          <ul className="select_options">
            {filteredOptions.length ? dropdownOptions : dropdownEmpty}
          </ul>
        </div>
      </div>

      <MultiSelectActions
        applyLabel={applyLabel}
        cancelLabel={cancelLabel}
        onApply={() => onApply(Array.from(temporarilySelectedKeys))}
        onCancel={onCancel}
      />
    </div>
  );
};
