import {
  TabList as BaseTabList,
  TabListProps as BaseTabListProps,
} from '@reach/tabs';
import type * as Polymorphic from '@reach/utils/polymorphic';
import classnames from 'classnames';
import { ComponentProps } from 'react';

export type TabListProps = ComponentProps<
  Polymorphic.MemoComponent<'div', BaseTabListProps>
>;

const TabList = ({ className, ...props }: TabListProps) => (
  <BaseTabList {...props} className={classnames('tabs_tabList', className)} />
);

export { TabList };
