import countryCodes from '../../../../../../storybook/PhoneCode/phoneCodesTemp.json';
import { SelectCountry, SelectCountryProps } from './index';

const countryCodesAll = countryCodes.all.reduce(
  (acc, val) => {
    return countryCodes.preferred.includes(val.code)
      ? { ...acc, preferred: [...acc.preferred, val] }
      : { ...acc, others: [...acc.others, val] };
  },
  {
    preferred: [],
    others: [],
  },
);

const BaseArgs = {
  name: 'selectCountry',
  label: 'Label',
  countryCodes: [...countryCodesAll.preferred, ...countryCodesAll.others],
};

export const SelectCountryArgTypes = {};

const Base = (args: SelectCountryProps) => <SelectCountry {...args} />;

const BaseWithoutWrapper = (args: SelectCountryProps) => (
  <div className="test-zone">
    <Base {...args} />
  </div>
);

const BaseWithWrapper = (args: SelectCountryProps) => (
  <div className="test-zone sb-select_safeZone">
    <Base {...args} />
  </div>
);

export const SelectCountryPlayground = BaseWithoutWrapper.bind({});;
SelectCountryPlayground.args = {
  ...BaseArgs,
};

export const SelectCountryExpanded = BaseWithWrapper.bind({});
SelectCountryExpanded.args = {
  ...BaseArgs,
  expanded: true,
};



