import ReactDOMServer from 'react-dom/server';
import addons, { makeDecorator } from '@storybook/addons';
import { html_beautify } from 'js-beautify';
import { ADDON_NAME, MARKUP_EVENT, PARAM_NAME } from './constants';

export default makeDecorator({
  name: ADDON_NAME,
  parameterName: PARAM_NAME,
  skipIfNoParametersOrOptions: false,
  wrapper: (getStory, context, { parameters }) => {
    const channel = addons.getChannel();

    const component = getStory();

    const initialHTML = ReactDOMServer.renderToStaticMarkup(component);
    let div = document.createElement('div');
    div.innerHTML = initialHTML;
    const element = div.getElementsByClassName('test-zone')[0];
    const html = element ? element.innerHTML : initialHTML;

    // Use your preferred HTML beautifier
    const markup = html_beautify(html, {
      indent_size: 2,
      inline: [],
    });

    channel.emit(MARKUP_EVENT, markup);

    return getStory(context);
  },
});
