// eslint-disable-next-line import/no-unresolved
import { ActionButton, IcCopy, useComponentVisible } from '@product/marshal';
import { color, colorBase } from '@product/marshal-tokens';
import classnames from 'classnames';

import { copyToClipboard } from '../lib/copyToClipboard';

const vals = Object.keys(colorBase)
  .map((c) => c.replace(/\D/g, ''))
  .map((uv) => parseInt(uv, 10))
  .sort((a, b) => b - a);
const keys = Object.keys(colorBase).map((cb) => cb.replace(/[0-9]/g, ''));
const uniqVals = [...new Set(vals)];
const uniqKeys = [...new Set(keys)];
document.documentElement.style.setProperty('--color-keys', uniqVals.length);

function maxDepth(object) {
  if (typeof object !== 'object' || object === null) {
    return 0;
  }

  const values = Object.values(object);

  return (
    (values.length && Math.max(...values.map((value) => maxDepth(value)))) + 1
  );
}

function getLuminosityFromHex(H) {
  // Convert hex to RGB first
  let r = 0;
  let g = 0;
  let b = 0;

  r = `0x${H[1]}${H[2]}`;
  g = `0x${H[3]}${H[4]}`;
  b = `0x${H[5]}${H[6]}`;

  // Then to HSL
  r /= 255;
  g /= 255;
  b /= 255;
  const cmin = Math.min(r, g, b);
  const cmax = Math.max(r, g, b);
  let l = 0;

  l = (cmax + cmin) / 2;
  l = +(l * 100).toFixed(1);

  return l;
}

const Swatch = ({
  colorEntry,
  className = 'sb-color_swatch',
  shortName = '',
  allowCopyClipboard = false,
}) => {
  const { visibleRef, isComponentVisible, setIsComponentVisible } =
    useComponentVisible(false);
  const threshold = 97.5;

  return (
    <div
      className={className}
      onClick={() => setIsComponentVisible(!isComponentVisible)}
      ref={visibleRef}
    >
      <span
        className={classnames('sb-color_sample', {
          'sb-color_sampleHighlight':
            getLuminosityFromHex(colorEntry[1]) > threshold,
        })}
        style={{ backgroundColor: `${colorEntry[1]}` }}
      />
      <p className="sb-color_name">{shortName || colorEntry[0]}</p>
      <p className="sb-color_code">{colorEntry[1]}</p>
      {isComponentVisible && allowCopyClipboard && (
        <div className="contextualPopup">
          <div className="contextualPopup-content">
            <ul>
              <li>
                <ActionButton
                  icon={IcCopy}
                  onClick={() => copyToClipboard(`color.$${colorEntry[0]}`)}
                  size="sm"
                >
                  SCSS format
                </ActionButton>
              </li>
              <li>
                <ActionButton
                  icon={IcCopy}
                  onClick={() => copyToClipboard(`$color-${colorEntry[0]}`)}
                  size="sm"
                >
                  SCSS format (old)
                </ActionButton>
              </li>
              <li>
                <ActionButton
                  icon={IcCopy}
                  onClick={() =>
                    copyToClipboard(
                      `@color/${colorEntry[0].replaceAll('-', '_')}`,
                    )
                  }
                  size="sm"
                >
                  Android format
                </ActionButton>
              </li>
            </ul>
          </div>
        </div>
      )}
    </div>
  );
};

const RenderSwatchesGrid = () =>
  uniqKeys.map((key) =>
    uniqVals.map((val) => {
      const colorEntries = Object.entries(colorBase);
      const colorKeys = Object.keys(colorBase);
      const index = colorKeys.indexOf(`${key}${val}`);

      if (index >= 0) {
        return (
          <Swatch colorEntry={colorEntries[index]} key={colorKeys[index]} />
        );
      }
      return (
        <div className="sb-color_swatch" key={index}>
          <span className="sb-color_sample is-empty" />
        </div>
      );
    }),
  );

const RenderSwatchesGridWithNames = ({ obj, name }) =>
  Object.entries(obj).map(([k, v]) => {
    const isObject = typeof v === 'object';
    const title = name ? `${name}-${k}` : k;
    return (
      <>
        {isObject ? (
          <h2
            className={classnames('sb-title', {
              [maxDepth(v) > 1
                ? 'sb-colorName_title'
                : 'sb-colorName_subtitle']: true,
            })}
            key={k}
          >
            {k}
          </h2>
        ) : null}
        {isObject ? (
          <RenderSwatchesGridWithNames key={title} name={title} obj={v} />
        ) : (
          <Swatch
            allowCopyClipboard
            className="sb-colorName_swatch"
            colorEntry={[title, v]}
            key={title}
            shortName={k}
          />
        )}
      </>
    );
  });

export const ColorSwatches = ({ className }) => (
  <div className={`sb-color ${className}`}>
    <RenderSwatchesGrid />
  </div>
);

export const ColorSwatchesWithNames = ({ className }) => (
  <div className={`sb-colorName ${className}`}>
    <RenderSwatchesGridWithNames obj={color} />
  </div>
);
