import { IconProps } from '@product/marshal-tokens';
import classnames from 'classnames';
import isEmpty from 'lodash/isEmpty';
import React, { forwardRef, useMemo } from 'react';
import type { PolymorphicPropsWithoutRef } from 'react-polymorphic-types';

export const ActionButtonDefaultElement = 'button';

type ActionButtonVariants = 'primary' | 'secondary' | 'destructive';

export type ActionButtonSizes = 'xs' | 'sm' | 'md' | 'lg';

export interface ActionButtonOwnProps {
  /**
   * Defines the look&feel of the button, changing the colors of the button and the label.
   * To choose between:
   */
  variant?: ActionButtonVariants;
  /**
   * Defines the size of the button and the label. To choose between:
   */
  size?: ActionButtonSizes;
  /**
   * Sets the icon to be displayed in the button. As of the current release, the icon must belong to one of those present in the Icon component.
   */
  icon?: React.ComponentType<IconProps>;
  /**
   * Sets whether the button is disabled or not. Having it disabled will prevent the user to click it
   * and will change the look&feel of the button and the label
   */
  disabled?: boolean;
  /**
   * Sets whether the button is loading or not. When true, the button will display a spinner instead
   * of the icon passed in the `icon` property
   */
  loading?: boolean;
}

export type ActionButtonProps<
  ET extends React.ElementType = typeof ActionButtonDefaultElement,
> = PolymorphicPropsWithoutRef<ActionButtonOwnProps, ET>;

const ForwardedRefActionButton = forwardRef(
  (
    {
      icon,
      children,
      variant = 'primary',
      size = 'md',
      disabled = false,
      loading = false,
      className,
      as,
      ...restProps
    }: ActionButtonProps<typeof ActionButtonDefaultElement>,
    ref: React.RefObject<HTMLButtonElement>,
  ) => {
    const Icon = icon;

    // Avoids the default attribute of button type = 'submit' that may trigger forms submit
    const appliedProps = isEmpty(as)
      ? { ...restProps, type: 'button' as const }
      : { ...restProps };

    const Element: React.ElementType = useMemo(
      () => as || ActionButtonDefaultElement,
      [as],
    );

    const variantClassName = useMemo(
      () => (variant ? `actionButton-${variant}` : ''),
      [variant],
    );

    return (
      <Element
        className={classnames(
          'actionButton',
          `actionButton-${size}`,
          className,
          {
            [`${variantClassName}`]: !!variant,
            'is-disabled': disabled,
            'is-loading': loading,
            'actionButton-noMargin': !children,
          },
        )}
        data-marshalcomponent="ActionButton"
        disabled={disabled}
        ref={ref}
        {...appliedProps}
      >
        {Icon && (
          <span className="actionButton_icon">
            {loading ? <div className="actionButton_loadingIcon" /> : <Icon />}
          </span>
        )}
        <span className="actionButton_content">{children}</span>
      </Element>
    );
  },
);

interface ActionButtonType {
  <ET extends React.ElementType = typeof ActionButtonDefaultElement>(
    props: ActionButtonProps<ET>,
  ): JSX.Element;
  displayName: string;
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const ActionButton: ActionButtonType = ForwardedRefActionButton as any;

ActionButton.displayName = 'ActionButton';

export { ActionButton };
