import { color, Icons } from '@product/marshal-tokens';

const renderIcon = () =>
  Object.keys(Icons).map((icon) => {
    const Icon = Icons[icon];
    return (
      <li className="sb-icons_item" key={icon}>
        <div className="sb-icons_wrapper">
          <div className="sb-icons_placeholder">
            <Icon
              color={color.action.light.primary}
              highlightColor={color.info.light.negative}
            />
          </div>
          <p className="sb-icons_desc">{icon}</p>
        </div>
      </li>
    );
  });

export const IconsCatalog = () => (
  <ul className="sb-icons_list sb-icons_list-grid">{renderIcon()}</ul>
);



