import { IcCircleCross } from '@product/marshal-tokens';
import { noop } from '@product/utils-drawer';
import { FC } from 'react';

export interface SelectOverlayProps {
  clearButtonText?: string;
  label?: string;
  onClear?: () => void;
  prefix?: string;
}

const SelectOverlay: FC<SelectOverlayProps> = ({
  label = '',
  prefix,
  clearButtonText,
  onClear = noop,
}) => (
  <div className="selectOverlay" data-testid="selectOverlay">
    <div className="selectOverlay_label">
      {prefix && <span className="selectOverlay_prefix">{prefix}</span>}
      <span className="selectOverlay_title">{label}</span>
    </div>
    <IcCircleCross
      aria-label={clearButtonText}
      className="selectOverlay_closeIcon"
      data-testid="selectOverlayCloseIcon"
      onClick={onClear}
    />
  </div>
);

export { SelectOverlay };
