import classNames from 'classnames';
import { HTMLProps, ReactElement } from 'react';

import { StepIndicatorItemProps } from './StepIndicatorItem';

type StepIndicatorItemChildren = ReactElement<StepIndicatorItemProps>;

export interface StepIndicatorListProps extends HTMLProps<HTMLUListElement> {
  children: StepIndicatorItemChildren | StepIndicatorItemChildren[];
}

export const StepIndicatorList = ({
  children,
  className,
  ...props
}: StepIndicatorListProps) => (
  <ul className={classNames('stepIndicator', className)} {...props}>
    {children}
  </ul>
);
