import React, {
  FC,
  ReactNode,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';

import { Tooltip, TooltipProps } from './Tooltip';

export interface EllipsisWithTooltipProps
  extends Omit<TooltipProps, 'title' | 'children'> {
  customContent?: ReactNode;
  alwaysPresent?: boolean;
}

const EllipsisWithTooltip: FC<EllipsisWithTooltipProps> = ({
  children,
  customContent,
  alwaysPresent,
  ...rest
}) => {
  const [isTooltipVisible, setIsTooltipVisible] = useState(true);
  const container = useRef() as React.MutableRefObject<HTMLDivElement>;

  const checkVisibility = useCallback(
    (event?: React.MouseEvent | React.FocusEvent) => {
      const current = event?.currentTarget ||
        container.current || {
          clientWidth: 0,
          scrollWidth: 0,
        };
      const isContainerReady =
        alwaysPresent || current.clientWidth < current.scrollWidth;
      setIsTooltipVisible(isContainerReady);
    },
    [alwaysPresent],
  );

  useEffect(() => {
    checkVisibility();
  }, [container, alwaysPresent, checkVisibility]);

  return (
    <div
      className="ellipsisWithTooltip"
      data-testid="ellipsisWithTooltip"
      onFocus={checkVisibility}
      onMouseOver={checkVisibility}
      ref={container}
    >
      {isTooltipVisible ? (
        <Tooltip title={customContent || children || ''} {...rest}>
          {children}
        </Tooltip>
      ) : (
        <>{children}</>
      )}
    </div>
  );
};

export { EllipsisWithTooltip };
