import MaterialTooltip from '@material-ui/core/Tooltip';
import classnames from 'classnames';
import React, { FC } from 'react';

export interface TooltipProps {
  testId?: string;
  className?: string;
  boxSize?: 'lg';
  placement?:
    | 'bottom-end'
    | 'bottom-start'
    | 'bottom'
    | 'left-end'
    | 'left-start'
    | 'left'
    | 'right-end'
    | 'right-start'
    | 'right'
    | 'top-end'
    | 'top-start'
    | 'top';
  open?: boolean;
  title: NonNullable<React.ReactNode>;
}

const Tooltip: FC<TooltipProps> = ({
  testId = 'tooltipContainer_children',
  children,
  className,
  placement = 'top',
  open,
  title,
  boxSize,
}) => {
  // default props for materialTooltip
  const enterDelay = 100;
  const enterNextDelay = 0;
  const enterTouchDelay = 700;
  const leaveDelay = 0;
  const leaveTouchDelay = 1500;

  const tooltipChildrenClassnames = classnames(
    'tooltipContainer_children',
    className,
  );

  const popperClasses = classnames({
    tooltipContainer: true,
    [`tooltipContainer-${boxSize!}`]: !!boxSize,
  });

  const defaultClassNames = {
    popper: popperClasses,
    arrow: 'tooltipContainer_arrow',
    popperArrow: '',
    popperInteractive: '',
    tooltip: 'tooltipContainer_inner',
    tooltipArrow: '',
    tooltipPlacementBottom: 'tooltipContainer_inner-placementBottom',
    tooltipPlacementLeft: 'tooltipContainer_inner-placementLeft',
    tooltipPlacementRight: 'tooltipContainer_inner-placementRight',
    tooltipPlacementTop: 'tooltipContainer_inner-placementTop',
    touch: '',
  };

  return (
    <MaterialTooltip
      arrow
      classes={defaultClassNames}
      data-marshalcomponent="Tooltip"
      enterDelay={enterDelay}
      enterNextDelay={enterNextDelay}
      enterTouchDelay={enterTouchDelay}
      leaveDelay={leaveDelay}
      leaveTouchDelay={leaveTouchDelay}
      open={open}
      placement={placement}
      tabIndex={0}
      title={title}
    >
      <span className={tooltipChildrenClassnames} data-testid={testId}>
        {children}
      </span>
    </MaterialTooltip>
  );
};

Tooltip.displayName = 'Tooltip';

export { Tooltip };
