import { copyToClipboard } from './copyToClipboard';

export const RequiredStyles = ({ paths }: { paths: string[] }) => {
  function renderFullPaths() {
    const fullPaths = paths.map(
      (path) => `@use "@product/marshal/dist/${path}";`,
    );
    return fullPaths.toString().replace(/,/g, '\n');
  }

  return (
    <ul
      className="sbdocs pointer sbdocs-requiredStyles"
      onClick={() => copyToClipboard(renderFullPaths())}
    >
      {paths.map((path) => (
        <li key={path}>
          <code>{path}</code>
        </li>
      ))}
    </ul>
  );
};
