import classnames from 'classnames';
import omit from 'lodash/omit';
import React, { forwardRef, useEffect, useRef, useState } from 'react';
import mergeRefs from 'react-merge-refs';

import { SelectionControlProps } from '../SelectionControl';

export interface SwitchProps extends SelectionControlProps {
  helper?: string | React.ReactNode;
  checkedLabel?: string;
  uncheckedLabel?: string;
}

export const Switch = forwardRef(
  (props: SwitchProps, ref: React.RefObject<HTMLInputElement>) => {
    const {
      className,
      label,
      checkedLabel,
      uncheckedLabel,
      description,
      disabled,
      helper,
      id,
      onChange,
    } = props;
    const [checked, setChecked] = useState(props.checked);
    const inputRef = useRef<HTMLInputElement>(null);
    const combinedRefs = mergeRefs([inputRef, ref]);
    const otherProps = omit(props, ['helper']);

    useEffect(() => {
      setChecked(props.checked);
    }, [props.checked]);

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      setChecked(e.target.checked);
      if (onChange) {
        onChange(e);
      }
    };

    return (
      <div
        className={classnames('formField', 'formField-switch', className, {
          'is-disabled': disabled,
        })}
        data-marshalcomponent="Switch"
      >
        <div className="formField_control">
          <input
            {...otherProps}
            checked={checked}
            className="formField_switch"
            onChange={handleChange}
            ref={combinedRefs}
            type="checkbox"
          />
          <label className="formField_label" htmlFor={id}>
            {checked ? checkedLabel || label : uncheckedLabel || label}
            {description && (
              <p className="formField_description">{description}</p>
            )}
          </label>
        </div>
        {helper && <p className="formField_helper">{helper}</p>}
      </div>
    );
  },
);

Switch.defaultProps = {
  disabled: false,
  checked: false,
};
