import { IcShow, IcShowOff } from '@product/marshal-tokens';
import classnames from 'classnames';
import React, { forwardRef, PropsWithRef, useEffect, useState } from 'react';

import { Input, InputProps } from '../Input';

interface PasswordOwnProps extends InputProps {
  passwordVisible?: boolean;
  EntropyComponent?: React.ReactNode;
}

export type PasswordProps = PropsWithRef<PasswordOwnProps>;

export const Password = forwardRef(
  (
    {
      passwordVisible: passwordVisibleProp,
      EntropyComponent,
      ...otherProps
    }: PasswordOwnProps,
    ref: React.RefObject<HTMLInputElement>,
  ) => {
    const [passwordVisible, setPasswordVisible] = useState(passwordVisibleProp);

    useEffect(() => {
      setPasswordVisible(passwordVisibleProp);
    }, [passwordVisibleProp]);

    const toggleVisibility = () => {
      setPasswordVisible(!passwordVisible);
    };

    const PasswordVisibility = () => {
      const VisibilityIcon = passwordVisible ? IcShowOff : IcShow;
      return (
        <div>
          <VisibilityIcon
            className="formField_icon formField_icon-visibility"
            onClick={toggleVisibility}
          />
        </div>
      );
    };

    return (
      <Input
        dataMarshalComponent="Password"
        {...otherProps}
        className={classnames(otherProps.className, 'formField-password')}
        PostComponent={
          <>
            {EntropyComponent}
            <PasswordVisibility />
          </>
        }
        ref={ref}
        type={passwordVisible ? 'text' : 'password'}
      />
    );
  },
);
