import classnames from 'classnames';
import { useMemo } from 'react';

import { Option, Select } from '../Select';
import { SelectOverlay } from './SelectOverlay';

export interface Item<ValueType extends string = string> {
  label: string;
  value: ValueType;
}

export interface SelectFilterProps<ValueType extends string = string> {
  label: string;
  labelPrefix?: string;
  onClear?: () => void;
  onItemSelected?: (itemValue: ValueType) => void;
  options: Item<ValueType>[];
  selectedItemValue?: ValueType;
  className?: string;
  filterClassName?: string;
  wrapperProps?: { className?: string };
  disabled?: boolean;
}

const SelectFilter = <ValueType extends string = string>({
  options = [],
  selectedItemValue,
  onItemSelected,
  labelPrefix,
  label,
  onClear,
  className,
  filterClassName,
  wrapperProps,
  disabled,
}: SelectFilterProps<ValueType>) => {
  const selectFilterClassNames = classnames('selectFilter', className);
  const selectedItem = useMemo(
    () => options.find(({ value }) => value === selectedItemValue),
    [options, selectedItemValue],
  );

  const optionsWithDefault = useMemo(
    () => [
      ...options,
      {
        label,
        value: '__blank__',
      },
    ],
    [options, label],
  );

  return (
    <div className={selectFilterClassNames} data-testid="selectFilter">
      {selectedItem && (
        <SelectOverlay
          label={selectedItem.label}
          onClear={onClear}
          prefix={labelPrefix}
        />
      )}
      <Select
        className={filterClassName}
        disabled={disabled}
        label={selectedItem?.label || label}
        name="selectFilter"
        onChange={onItemSelected}
        size="sm"
        value={selectedItem?.value || '__blank__'}
        wrapperProps={wrapperProps}
      >
        {optionsWithDefault.map((option) => (
          <Option key={option.value} value={option.value}>
            {option.label}
          </Option>
        ))}
      </Select>
    </div>
  );
};

export { SelectFilter };
