import classnames from 'classnames';
import React, { SyntheticEvent } from 'react';

import { buildAvatarClassName, buildAvatarInitials } from './utils';

export type AvatarSize = 'sm' | 'md' | 'lg' | 'xl' | 'xxl' | 'xxxl';

export interface AvatarProps {
  id?: string;
  className?: string;
  name: string;
  surname: string;
  avatarUrl?: string;
  children?: React.ReactNode;
  size?: AvatarSize;
  dataTestId?: string;
}

const Avatar = ({
  id = '0',
  className,
  name,
  surname,
  avatarUrl,
  children,
  dataTestId = 'avatar',
  size = 'md',
}: AvatarProps) => {
  const initials = buildAvatarInitials(name, surname);

  const avatarClassnames = classnames(
    'avatar',
    buildAvatarClassName(id),
    className,
    {
      [`avatar-${size}`]: size,
    },
  );

  const hideBrokenImage = (ev: SyntheticEvent<HTMLImageElement>) => {
    ev.currentTarget.setAttribute('style', 'display:none');
  };

  return (
    <figure
      className={avatarClassnames}
      data-marshalcomponent="Avatar"
      data-testid={dataTestId}
    >
      <span className="avatar_initials">{initials}</span>
      {avatarUrl && (
        <img
          alt={`${name} ${surname} avatar`}
          className="avatar_image"
          onError={hideBrokenImage}
          src={avatarUrl}
        />
      )}
      {children}
    </figure>
  );
};

export { Avatar };
